<template>
<div class="user-list">
    <div class="head">
        <span>日期范围：</span>
        <my-datepicker @changed="dateChange" ref="dateStart" time="start"></my-datepicker> - 
        <my-datepicker @changed="dateChange" ref="dateEnd" time="end"></my-datepicker>&nbsp;&nbsp;
        <input type="text" placeholder="姓名或电话号码" v-model="parame.queryStr" />
        <span @click="getData" class="btn">搜索</span>
    </div>
    <table>
        <thead>
            <tr>
                <th>手机号码</th>
                <th>登录名</th>
                <th>姓名</th>
                <th>邀请人</th>
                <th>注册时间</th>
                <th>最后登录时间</th>
                <th>操作</th>
            </tr>
        </thead>
            <tr v-for="(item, index) in dataList">
                <td><router-link :to="{path: '/user-detail', query: {id: item.id}}">{{ item.mobile }}</router-link></td>
                <td>{{ item.loginName }}</td>
                <td>{{ item.userName }}</td>
                <td>{{ item.refMobile }}</td>
                <td>{{ item.registerDate | date }}</td>
                <td>{{ item.lastLoginDate | date }}</td>
                <td><a @click="edite(item)" href='javascript:void(0);'>维护</a></td>
            </tr>
            <tr v-if="!dataList.length && isFinished">
                <td colspan="7">暂无数据</td>
            </tr>
        <tbody>
        </tbody>
    </table>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <edite-user @closeAlert="editeClose" v-if="editeShow" :parent-data="editeData"></edite-user>
</div>
</template>

<script>
import myDatepicker from '../../submodel/my-datepicker.vue'
import alert from '../../submodel/alert.vue'
import pagination from '../../submodel/pagination.vue'
import editeUser from '../edite-user/edite-user.vue'
export default {
    name: 'user-list',
    data () {
        return {
            dataList: [],
            parame: {
                now: 1,
                all: 1,
                start: '',
                end: '',
                queryStr: ''
            },
            editeData: {},
            editeShow: false,
            isFinished: false
        }
    },
    components: {
        myDatepicker,
        alert,
        pagination,
        editeUser
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/user/searchUserList', {
                page: {
                    'pageNo': this.parame.now,
                    'pageSize': 10,
                    'strStartTime': this.parame.start,
                    'strEndTime': this.parame.end,
                    params: {
                        'condition': this.parame.queryStr
                    }
                }
            }, function (data) {
                This.dataList = data.data.page.results
                This.parame.all = data.data.page.totalPage
                This.isFinished = true
            }, function (err) {
                console.log(err)
            })
        },
        edite (obj) {
            this.editeShow = true
            this.editeData = obj
        },
        editeClose () {
            this.editeShow = false
        },
        dateChange (val, type) {
            this.parame[type] = val.time
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            console.log('cencel')
        },
        makesureAlert () {
            console.log('ok')
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getData()
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.user-list {
    position: relative;
}
.head {
    padding: 10px 0;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
input {
    border: 1px solid #e4e4e4;
    padding: 6px 10px;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
</style>



