<template>
<div class="new-artical">
    <h4>编写文章</h4>
    <div class="row">
        <p>所属栏目：</p>
        <selection :index-default="columuIndex" keystr="name" :arr="columuList" @selected="selectionCallback"></selection>
    </div>
    <div class="row">
        <p>文章标题：</p>
        <input v-model="dataObj.title" type="text" />
    </div>
    <div class="row">
        <p>作者：</p>
        <input v-model="dataObj.author" type="text" />
    </div>
    <div class="row">
        <p>来源：</p>
        <input v-model="dataObj.source" type="text" />
    </div>
    <div class="row">
        <p>来源链接：</p>
        <input v-model="dataObj.sourceLink" type="text" />
    </div>
    <vue-ueditor style="width: 1000px;margin: 0 auto" ueditorPath="/static/utf8-jsp/" @ready="editorReady"></vue-ueditor>
    <div class="btn">
        <span @click="saveFn">发布</span>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import vueUeditor from 'vue-ueditor'
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'new-artical',
    data () {
        return {
            columuList: [],
            columuIndex: 0,
            dataObj: {
                title: '',
                author: '',
                source: '',
                sourceLink: '',
                articalContent: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getColumn()
        })
    },
    methods: {
        getColumn () {
            let This = this
            this.$ajax('/back/cms/queryCmsColumnByParams', {}, function (data) {
                if (data.data.message.code === 1000) {
                    This.columuList = data.data.columnvos
                    let id = This.$route.query.id
                    if (id) {
                        This.getArtical()
                    }
                }
            }, function (err) {
                console.log(err)
            })
        },
        getArtical () {
            let This = this
            this.$ajax('/back/article/getArticleById', {
                article: {
                    id: this.$route.query.id
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.dataObj.title = data.data.article.title
                    This.dataObj.author = data.data.article.author
                    This.dataObj.source = data.data.article.source
                    This.dataObj.sourceLink = data.data.article.sourceLink
                    This.dataObj.articalContent = data.data.article.content
                    This.columuIndex = This.getIndex(data.data.article.columnName, this.columuList, 'name')
                }
            }, function (err) {
                console.log(err)
            })
        },
        saveFn () {
            let This = this
            let id = this.$route.query.id
            let url = id ? '/back/article/modifyArticleByParams' : '/back/article/addArticle'
            this.$ajax(url, {
                article: {
                    id: id,
                    columnId: this.columuList[this.columuIndex].id,
                    columnName: this.columuList[this.columuIndex].name,
                    title: this.dataObj.title,
                    author: this.dataObj.author,
                    source: this.dataObj.source,
                    sourceLink: this.dataObj.sourceLink,
                    content: this.dataObj.articalContent
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    setTimeout(() => {
                        This.$router.push({path: 'cms-artical'})
                    }, 1500)
                }
            }, function (err) {
                console.log(err)
            })
        },
        selectionCallback (index) {
            this.columuIndex = index
        },
        editorReady (editorInstance) {
            let This = this
            setTimeout(() => {
                editorInstance.setContent(this.dataObj.articalContent)
            }, 1000)
            editorInstance.addListener('contentChange', () => {
                This.dataObj.articalContent = editorInstance.getContent()
            })
        },
        getIndex (val, arr, key) {
            let i = 0
            arr.forEach(function (item, index) {
                if (item[key] === val) {
                    i = index
                }
            })
            return i
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        vueUeditor,
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.row {
    width: 580px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    p {
        text-align: right;
        width: 200px;
    }
}
h4 {
    padding-left: 40px;
    border-bottom: 1px solid #38afff;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.btn {
    padding: 30px 0;
    text-align: center;
    span {
        display: inline-block;
        padding: 6px 30px;
        background-color: #38afff;
        border-radius: 4px;
        color: #fff;
        cursor: pointer;
        text-align: center;
    }
}
</style>



