<template>
<div class="full-review">
    <div class="item-style" v-for="(item, index) in dataList">
        <p class="item-title">
            <span class="title-style">{{ item.title }}</span>
            <span @click="settleFn(item, index)" class="examine">审核结算</span>
            <span @click="cancleFn(item, index)" class="flow-btn">流标</span>
        </p>
        <div class="row set-bg">
            <div>
                <p>借款人：{{ item.userName }}</p>
                <p>已投金额：{{ item.bidAmount }}元</p>
            </div>
            <div>
                <p>投标数：{{ item.bidNumber }}</p>
                <p>总金额：{{ item.amount }}元</p>
            </div>
            <div>
                <p>用时：{{ item.raiseTime | time }}</p>
                <p>状态：已满标</p>
            </div>
        </div>
    </div>
    <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames"></alert>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'full-review',
    data () {
        return {
            dataList: [],
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            alertParames: {
                isShow: false,
                width: 300,
                title: '',
                content: '',
                cancel: '取消',
                makesure: '确定',
                contentStyle: 'text-align: center;',
                time: ''
            },
            cancelData: {}
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    filters: {
        time: function (t) {
            let time = parseInt(parseInt(t) / 1000)
            return Math.floor(time / 86400) + '天' + Math.floor(time % 86400 / 3600) + '时' + Math.floor(time % 86400 % 3600 / 60) + '分' + time % 60 + '秒'
        }
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/loan/loanList', {
                page: {
                    pageSize: 100,
                    params: {
                        status: 'FINISHED'
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.dataList = data.data.page.results || []
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        settleFn (obj, index) {
            let This = this
            This.msg.content = '审核结算中...'
            This.msg.isShow = true
            this.$ajax('/back/loan/settleLoan', {
                loan: {
                    id: obj.id
                }
            }, function (data) {
                This.msg.isShow = false
                setTimeout(() => {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }, 0)
                if (data.data.message.code === 1000) {
                    This.dataList.splice(index, 1)
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        cancleFn (obj, index) {
            this.alertParames.isShow = true
            this.alertParames.content = '你确定要取消该标的吗？'
            this.cancelData = obj
        },
        closeMsg () {
            this.msg.isShow = false
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            this.alertParames.isShow = false
        },
        makesureAlert () {
            let This = this
            setTimeout(() => {
                this.alertParames.isShow = false
            }, 0)
            This.msg.content = '流标中...'
            This.msg.isShow = true
            this.$ajax('/back/loan/failedLoan', {
                loan: {
                    'id': this.cancelData.id
                }
            }, function (data) {
                This.msg.isShow = false
                setTimeout(() => {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }, 0)
                if (data.data.message.code === 1000) {
                    This.getData()
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.item-style {
    border-bottom: 1px solid #999;
}
.item-title {
    color: #666;
}
.title-style {
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}
.examine, .flow-btn {
    padding: 5px 12px;
    margin-left: 30px;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
}
.examine {
    background-color: #38afff;
}
.flow-btn {
    background-color: #ff9c3a;
}
.row {
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: #666;
}
.set-bg {
    border-top: 2px solid #999;
    background-color: #f3f3f3;
}
.slide {
    text-align: center;
    border-bottom: 1px solid #999;
    height: 30px;
}
.slide span {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    padding: 0 28px;
    color: #fff;
    background-color: #666;
    font-size: 16px;
    cursor: pointer;
}
</style>



