<template>
<div class="search-wrap">
    <slot name="other"></slot>
    <slot name="time">
        <span class="setColor">日期范围：</span>
        <my-datepicker @changed="dateChange" ref="dateStart" time="start"></my-datepicker> - 
        <my-datepicker @changed="dateChange" ref="dateEnd" time="end"></my-datepicker>
    </slot>
    <slot name="btn">
        <span @click="search" class="btn">查询</span>
    </slot>
</div>
</template>

<script>
import myDatepicker from './my-datepicker.vue'
export default {
    name: 'search-wrap',
    data () {
        return {
            start: '',
            end: ''
        }
    },
    components: {
        myDatepicker
    },
    methods: {
        dateChange (val, type) {
            this[type] = val.time
            this.$emit('changeVal', this.start, this.end)
        },
        search () {
            this.$emit('searchFn')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.search-wrap {
    padding: 10px 0;
}
.setColor {
    color: #333;
    font-size: 14px;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
input {
    border: 1px solid #e4e4e4;
}
</style>



