<template>
<div class="project">
    <div class="head">
        <p>项目列表</p>
        <a @click="newFn" href="javascript:">新建项目</a>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>项目名称</th>
                    <th>关联企业</th>
                    <th>借款人</th>
                    <th>总金额（元）</th>
                    <th>剩余额度（元）</th>
                    <th>借款期限（月）</th>
                    <th>还款方式</th>
                    <th>发起时间</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in dataList">
                    <td><a @click="toDetail(item)">{{ item.title }}</a></td>
                    <td>{{ item.guaranteeRealm }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.amount }}</td>
                    <td>{{ item.surplusAmount }}</td>
                    <td>{{ item.months }}</td>
                    <td>{{ item.method | method }}</td>
                    <td>{{ item.timeSubmit | date }}</td>
                    <td>
                        <template v-if="item.surplusAmount">
                            <a @click="applyFn(index)" v-if="!item.status" href="javascript:">发标申请</a>
                            <a @click="editProject(index)" v-if="!item.status" href="javascript:">编辑</a>
                            <a  @click="hiddenFn(item)" href="javascript:">{{ item.status?'解冻':'冻结' }}</a>
                        </template>
                    </td>
                </tr>
                <tr v-if="!dataList.length">
                    <td colspan="9">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <pagination v-if="all>1" class="paging" @pagingFn="pagingCallback" :now="now" :all="all"></pagination>
    <new-project v-if="newData.isShow" @back="goback" :edit-data="dataList[index]" :flag="newData.flag" class="new-project"></new-project>
    <project-detail @backFn="detailback" class="new-project" v-if="detailFrameShow" :data-obj="detailData"></project-detail>
    <new-bid v-if="newBidData.isShow" @back="newBidBack" :edit-data="dataList[index]" class="new-project"></new-bid>
    <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames"></alert>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import pagination from '../submodel/pagination.vue'
import alert from '../submodel/alert.vue'
import newProject from './project-new.vue'
import newBid from './bid-new.vue'
import projectDetail from './project-detail.vue'
export default {
    name: 'project',
    data () {
        return {
            dataList: [],
            index: 0,
            now: 1,
            all: 1,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            alertParames: {
                isShow: false,
                width: 300,
                title: '',
                content: '',
                cancel: '取消',
                makesure: '确定',
                contentStyle: 'text-align: center;',
                time: ''
            },
            newData: {
                isShow: false,
                flag: '1'
            },
            hiddenData: {},
            newBidData: {
                isShow: false
            },
            detailData: {},
            detailFrameShow: false
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/project/queryAllProjects', {
                page: {
                   'pageNo': this.now,
                   'pageSize': 10
                }
            }, function (data) {
                This.dataList = data.data.page.results
                This.all = data.data.page.totalPage
            }, function (err) {
                console.log(err)
            })
        },
        newFn () {
            this.newData.isShow = true
            this.newData.flag = '1'
        },
        editProject (index) {
            this.newData.isShow = true
            this.newData.flag = '2'
            this.index = index
        },
        applyFn (index) {
            this.newBidData.isShow = true
            this.index = index
        },
        hiddenFn (obj) {
            this.alertParames.isShow = true
            this.alertParames.content = obj.status ? '你确定要解冻该用户吗？' : '你确定要冻结该用户吗？'
            this.hiddenData = obj
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            this.alertParames.isShow = false
        },
        makesureAlert () {
            this.alertParames.isShow = false
            let This = this
            this.$ajax('/back/project/modifyProject', {
                project: {
                   'status': (This.hiddenData.status === 1) ? 0 : 1,
                   'id': This.hiddenData.id
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                This.getData()
            }, function (err) {
                console.log(err)
            })
        },
        pagingCallback (num) {
            this.now = num
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        },
        goback () {
            this.newData.isShow = false
            this.getData()
        },
        newBidBack () {
            this.newBidData.isShow = false
            this.getData()
        },
        toDetail (obj) {
            this.detailFrameShow = true
            this.detailData = obj
        },
        detailback () {
            this.detailFrameShow = false
        }
    },
    components: {
        pagination,
        alert,
        newProject,
        newBid,
        projectDetail
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.project {
    position: relative;
}
.head {
    display: flex;
    justify-content: space-between;
    p {
        margin: 0;
    }
    p, a {
        display: block;
        padding: 6px 12px;
        font-size: 18px;
    }
    a {
        color: #38afff;
        &:hover {
            text-decoration: underline;
        }
    }
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.new-project {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #fff;
}
.hidden {
    color: #000;
}
</style>



