<template>
<div class="generate-contract">
    <div>
        <table>
            <thead>
                <tr>
                    <th>标的名称</th>
                    <th>标的金额（元）</th>
                    <th>状态</th>
                    <th>结算时间</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td>{{ item.title }}</td>
                    <td>{{ item.amount }}</td>
                    <td>已结算</td>
                    <td>{{ item.timeSettled | date }}</td>
                    <td @click="generateContracts(item)">
                        <a href="javascript:">生成合同</a>
                    </td>
                </tr>
                <tr v-if="!list.length && isFinished">
                    <td colspan="5">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <pagination v-if="all>1" class="paging" @pagingFn="pagingCallback" :now="now" :all="all"></pagination>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'generate-contract',
    data () {
        return {
            list: [],
            now: 1,
            all: 1,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            isFinished: false
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            This.msg.content = '查询中...'
            This.msg.isShow = true
            this.$ajax('/back/contract/getuncontractLoans', {
                page: {
                    pageNo: this.now,
                    pageSize: 10
                }
            }, function (data) {
                This.msg.isShow = false
                This.isFinished = true
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results || []
                    This.all = data.data.page.totalPage
                } else {
                    setTimeout(() => {
                        This.msg.content = data.data.message.message
                        This.msg.time = '1500'
                        This.msg.isShow = true
                    }, 0)
                }
            }, function (err) {
                This.msg.isShow = false
                This.isFinished = true
                console.log(err)
            })
        },
        generateContracts (obj) {
            let This = this
            This.msg.content = '合同生成中...'
            This.msg.isShow = true
            this.$ajax('/back/contract/generateContracts', {
                loanId: obj.id
            }, function (data) {
                This.msg.isShow = false
                setTimeout(() => {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }, 0)
                if (data.data.message.code === 1000) {
                    This.getData()
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        pagingCallback (num) {
            this.now = num
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        pagination,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



