<template>
<div class="add-org">
	<div class="pop-up">
		<i @click="closeFn">X</i>
		<ul class="nav">
			<li @click="isShow = 1" :class="{active: isShow === 1}">查看</li>
			<li @click="isShow = 2"  :class="{active: isShow === 2}">编辑</li>
			<li @click="isShow = 3"  :class="{active: isShow === 3}">添加</li>
		</ul>
		<div class="org-content">
			<div v-show="isShow === 1" class="check-link">
				<router-link :to="{path: '/staff-list'}">查看员工列表</router-link>
			</div>
			<div v-show="isShow === 2" class="editor">
			    <div class="row">
			        <p>名称：</p>
			        <input v-model="editeData.title" type="text">
			    </div>
			    <div class="row">
			        <p>描述：</p>
			        <input v-model="editeData.describe" type="text">
			    </div>
			    <div class="row">
			        <p>服务热线：</p>
			        <input v-model="editeData.hotline" type="text">
			    </div>
			    <div class="row">
			        <p>地址：</p>
			        <input v-model="editeData.address" type="text">
			    </div>
			    <div class="row">
			        <p>是否删除：</p>
			        <selection :index-default="delableIndex" :arr="delable" @selected="delableFn"></selection>
			    </div>
			    <div class="btn">
					<span @click="closeFn" class="cancel">取消</span>
					<span @click="editeFn" class="makesure">确定</span>
				</div>
			</div>
			<div v-show="isShow === 3" class="add">
				<div class="row">
			        <p>名称：</p>
			        <input v-model="addData.title" type="text">
			    </div>
			    <div class="row">
			        <p>描述：</p>
			        <input v-model="addData.describe" type="text">
			    </div>
			    <div class="row">
			        <p>服务热线：</p>
			        <input v-model="addData.hotline" type="text">
			    </div>
			    <div class="row">
			        <p>地址：</p>
			        <input v-model="addData.address" type="text">
			    </div>
			    <div class="btn">
					<span @click="closeFn" class="cancel">取消</span>
					<span @click="addFn" class="makesure">确定</span>
				</div>
			</div>
		</div>
	</div>
	<alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'add-org',
    props: {
    	obj: {
    		type: Object,
    		default () {
    			return {}
    		}
    	}
    },
	data () {
	    return {
	    	editeData: {
	    		id: '',
	    		title: '',
	    		describe: '',
	    		hotline: '',
	    		address: ''
	    	},
	    	delable: [
	    		{
                    lable: '是',
                    val: true
                },
                {
                    lable: '否',
                    val: false
                }
	    	],
	    	delableIndex: 0,
	    	addData: {
	    		parantOrgId: '',
	    		level: '',
	    		title: '',
	    		describe: '',
	    		hotline: '',
	    		address: ''
	    	},
	    	isShow: 1,
	    	msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
	    }
	},
	components: {
		selection,
		alert
	},
	created () {
		this.$nextTick(() => {
			this.editeData.title = this.obj.orgName || ''
			this.editeData.describe = this.obj.description || ''
			this.editeData.hotline = this.obj.telphone || ''
			this.editeData.address = this.obj.address || ''
			this.editeData.id = this.obj.id || ''
			this.addData.parantOrgId = this.obj.id || ''
			this.addData.level = this.obj.level || ''
			this.delableIndex = this.getIndex(this.obj.del, this.delable, 'val')
		})
	},
	methods: {
		editeFn () {
			let This = this
			this.$ajax('/back/org/modifyOrganization', {
				'orgName': this.editeData.title,
				'description': this.editeData.describe,
				'address': this.editeData.address,
				'telphone': this.editeData.hotline,
				'del': this.delable[this.delableIndex].val,
				'id': this.editeData.id
			}, function (data) {
				This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                	setTimeout(() => {
                		This.$emit('closePopup', 'b')
                	}, 1500)
                }
            }, function (err) {
                console.log(err)
            })
		},
		addFn () {
			let This = this
			this.$ajax('/back/org/addOrganization', {
				'orgName': this.addData.title,
				'description': this.addData.describe,
				'address': this.addData.address,
				'telphone': this.addData.hotline,
				'parantOrgId': this.addData.parantOrgId,
				'level': this.addData.level
			}, function (data) {
				This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                	setTimeout(() => {
                		This.$emit('closePopup', 'b')
                	}, 1500)
                }
            }, function (err) {
                console.log(err)
            })
		},
		delableFn (index) {
			this.delableIndex = index
		},
		getIndex (val, arr, key) {
            let i = 0
            arr.forEach(function (item, index) {
                if (item[key] === val) {
                    i = index
                }
            })
            return i
        },
        closeFn () {
        	this.$emit('closePopup', 'a')
        },
        closeMsg () {
            this.msg.isShow = false
            this.closeFn()
        }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.add-org {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    text-align: center;
    &:before {
        content: '';
        height: 100%;
        display: inline-block;
        vertical-align: middle;
    }
    .pop-up {
        position: relative;
        padding-bottom: 10px;
        background: #fff;
        display: inline-block;
        vertical-align: middle;
        i {
            position: absolute;
            font-style: normal;
            padding: 4px;
            top: 8px;
            right: 8px;
            width: 14px;
            height: 14px;
            line-height: 14px;
            text-align: center;
            font-weight: 400;
            font-size: 14px;
            color: #fff;
            cursor: pointer;
            border-radius: 50%;
            background-color: #ff6f0a;
        }
    }
}
.nav {
	padding: 10px 0;
	display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    border-bottom: 1px solid #ddd;
    li {
        text-align: center;
        width: 120px;
        cursor: pointer;
        font-size: 16px;
    }
    .active, li:hover {
    	color:#ff6f0a;
    }
}
.check-link {
	text-align: center;
	padding: 10px 0;
}
.row {
    width: 400px;
    margin: 10px auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    p {
        text-align: right;
        width: 100px;
    }
    textarea {
        outline: none;
        padding: 10px;
    }
}
.btn {
	display: flex;
    justify-content: space-around;
    align-items: center;
    span {
    	display: block;
    	width: 50%;
    	padding: 6px 0;
    	cursor: pointer;
    }
    .makesure {
    	color: #289cff;
    }
	.cancel {
		color: #ff6f0a;
	}
}
.tree {
  	height: 600px;
  	width: 100%;
}
input[type="text"] {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



