<template>
<div class="today">
    <table>
        <thead>
            <tr>
                <th>标的名称</th>
                <th>借款人</th>
                <th>期数</th>
                <th>待还金额</th>
                <th>标的账户余额</th>
                <th>到期日</th>
                <th>借款人是否还款</th>
                <th>操作</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ item.title }}</td>
                <td>{{ item.userName }}</td>
                <td>{{ item.currentPeriod }}</td>
                <td>{{ item.amountInterest + item.amountPrincipal }}</td>
                <td>{{ item.account_aviamount }}</td>
                <td>{{ item.dueDate }}</td>
                <td>{{ item.isRepay ? "已还" : "未还" }}</td>
                <td><a v-if="item.isRepay" @click="payFn(item)" href='javascript:void(0);'>标的还款</a></td>
            </tr>
            <tr v-if="!list.length && isFinished">
                <td colspan="8">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <pagination v-if="all>1" class="paging" @pagingFn="pagingCallback" :now="now" :all="all"></pagination>
    <repayment-popup @pay="payfor" @closePopup="isShow = false" :detail="payDate" :repay-type="repayment" :is-show="isShow"></repayment-popup>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import repaymentPopup from '../../submodel/repayment-popup.vue'
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'today',
    data () {
        return {
            list: [],
            now: 1,
            all: 1,
            repayment: 'repayment',
            payDate: {},
            isShow: false,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            isFinished: false
        }
    },
    components: {
        repaymentPopup,
        pagination,
        alert
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/loanRepayment/searchLoanRepaymentList', {
                page: {
                    'strStartTime': '',
                    'strEndTime': '',
                    'pageNo': this.now,
                    'pageSize': 10,
                    params: {
                        'status': 'UNDUE'
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results
                    This.all = data.data.page.totalPage
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
                This.isFinished = true
            }, function (err) {
                console.log(err)
            })
        },
        payFn (obj) {
            this.payDate = obj
            this.isShow = true
        },
        payfor () {
            this.isShow = false
        },
        pagingCallback (index) {
            this.now = index
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.paging {
    padding: 10px 20px;
    text-align: right;
}
</style>



