<template>
<div class="ptkhbh">
    <p>
        手机号：<input v-model="parame.mobile" type="text" placeholder="必填" size="30"/>
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>客户编号</th>
                    <th>账户状态</th>
                    <th>是否绑卡</th>
                    <th>可用状态</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <th>{{ obj.platcust }}</th>
                    <th>{{ obj.status }}</th>
                    <th>{{ obj.is_card_bind }}</th>
                    <th>{{ obj.available_status }}</th>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'ptkhbh',
    data () {
        return {
            obj: {},
            parame: {
                mobile: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.checkMobile(this.parame.mobile)) {
                This.msg.content = '请输入正确手机号'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchPlatCust', {
                'mobile': this.parame.mobile
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.account || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



