<template>
<div class="invitation-ref">
    <div class="invitation-wrap">
        <div class="close-bar">
            <span class="export">导出</span>
            <i @click="closeFn">x</i>
        </div>
        <table>
            <thead>
                <tr>
                    <th>投资人</th>
                    <th>手机号码</th>
                    <th>投资金额</th>
                    <th>标的名称</th>
                    <th>利率</th>
                    <th>周期（月）</th>
                    <th>投资时间</th>
                    <th>结算时间</th>
                    <th>投标状态</th>
                    <th>年化投资金额</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td>{{ item.inviteName }}</td>
                    <td>{{ item.mobile }}</td>
                    <td>{{ item.investAmount }}</td>
                    <td>{{ item.title }}</td>
                    <td>{{ item.rate }} <span class="add-rate" v-if="item.addRate">+ {{ item.addRate }}</span></td>
                    <td>{{ item.months }}</td>
                    <td>{{ item.submitTime }}</td>
                    <td>{{ item.timeSettled }}</td>
                    <td>{{ item.status }}</td>
                    <td>{{ item.yearAmount }}</td>
                </tr>
                <tr v-if="!list.length">
                    <td colspan="10">暂无数据</td>
                </tr>
            </tbody>
        </table>
        <pagination v-if="all>1" class="paging" @pagingFn="pagingCallback" :now="now" :all="all"></pagination>
    </div>
    
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'invitation-ref',
    props: {
        obj: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data () {
        return {
            list: [],
            all: 1,
            now: 1,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            This.msg.content = '查询中...'
            This.msg.isShow = true
            this.$ajax('/back/statistic/queryUserStatisticDetail', {
                invitePage: {
                    pageNo: this.now,
                    pageSize: 10,
                    params: {
                        mobile: this.obj.mobile
                    }
                }
            }, function (data) {
                This.msg.isShow = false
                if (data.data.message.code === 1000) {
                    This.list = data.data.inviteStatisticVo.results || []
                    This.all = data.data.inviteStatisticVo.totalPage
                } else {
                    setTimeout(() => {
                        This.msg.content = data.data.message.message
                        This.msg.time = '1500'
                        This.msg.isShow = true
                    }, 0)
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        },
        closeFn () {
            this.$emit('close')
        }
    },
    components: {
        pagination,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.invitation-ref {
    position: fixed;
    left: 0;
    top: 0;
    width: 80%;
    height: 80%;
    padding: 10%;
    background-color: rgba(0,0,0,0.7);
}
.invitation-wrap {
    padding: 0 10px 10px;
    background-color: #fff;
}
.export {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #ff5825;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.inline {
    display: inline-block;
}
.close-bar {
    position: relative;
    padding: 10px 0;
}
i {
    position: absolute;
    font-style: normal;
    top: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-weight: 700;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    border-radius: 50%;
    background-color: #ff5825;
}
</style>



