<template>
<div class="bid-detail">
    <p @click="goback" class="goback">返回</p>
    <p class="head">
        <span>{{ dataList.loan.title }}</span>
    </p>
    <div>
        <table>
            <tbody>
                <tr>
                    <th>标的id：</th>
                    <td>{{ dataList.loan.id }}</td>
                    <th>产品类型：</th>
                    <td>{{ dataList.loan.productName }}</td>
                    <th>标的总金额（元）：</th>
                    <td>{{ dataList.loan.amount }}</td>
                </tr>
                <tr>
                    <th>借款人ID：</th>
                    <td>{{ dataList.loan.guaranteeId }}</td>
                    <th>关联企业：</th>
                    <td>{{ dataList.loan.guaranteeRealm }}</td>
                    <th>借款期限（月）：</th>
                    <td>{{ dataList.loan.months }}</td>
                </tr>
                <tr>
                    <th>年利率：</th>
                    <td>{{ dataList.loan.rate }} <span class="add-rate" v-if="dataList.loan.addRate"> + {{ dataList.loan.addRate }}</span> </td>
                    <th>还款方式：</th>
                    <td>{{ dataList.loan.method |  method }}</td>
                    <th>投资人群：</th>
                    <td>{{ dataList.loan.groupId ? '新用户' : '全部用户' }}</td>
                </tr>
                <tr>
                    <th>起投金额（元）：</th>
                    <td>{{ dataList.loan.minAmount }}</td>
                    <th>最大投资金额（元）：</th>
                    <td>{{ dataList.loan.maxAmount }}</td>
                    <th>投资增量（元）：</th>
                    <td>{{ dataList.loan.stepAmount }}</td>
                </tr>
                <tr>
                    <th>担保费率：</th>
                    <td>{{ dataList.loan.loanGuaranteeFee }}</td>
                    <th>服务费率：</th>
                    <td>{{ dataList.loan.loanServiceFee }}</td>
                    <th>风险管理费率：</th>
                    <td>{{ dataList.loan.loanRiskFee }}</td>
                </tr>
                <tr>
                    <th>借款管理费率：</th>
                    <td>{{ dataList.loan.loanManageFee }}</td>
                    <th>还款（借款）利息管理费率：</th>
                    <td>{{ dataList.loan.loanInterestFee }}</td>
                    <th>回款（投资）利息管理费率：</th>
                    <td>{{ dataList.loan.investInterestFee }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <p class="bid-detail-p">公司简介：<span>{{ dataList.projectDtail.project.firmInfo }}</span></p>
    <p class="bid-detail-p">经营范围：<span>{{ dataList.projectDtail.project.operationRange }}</span></p>
    <p class="bid-detail-p">资金用途：<span>{{ dataList.projectDtail.project.description }}</span></p>
    <p class="bid-detail-p">还款来源：<span>{{ dataList.projectDtail.project.repaySource }}</span></p>
    <p class="bid-detail-p">风险措施：<span>{{ dataList.projectDtail.project.riskInfo }}</span></p>
    <p>
        <!-- <img src="../../img/errorBg.png" alt="图片" />
        <img src="../../img/errorBg.png" alt="图片" />
        <img src="../../img/errorBg.png" alt="图片" />
        <img src="../../img/iconBg.gif" alt="图片" /> -->
    </p>
    <div>
        <ul class="nav">
            <li @click="navIndex=0" :class="{active: navIndex===0}">还款计划</li>
            <li @click="navIndex=1" :class="{active: navIndex===1}">投资记录</li>
        </ul>
        <table v-if="navIndex===0">
            <thead>
                <tr>
                    <th>期数</th>
                    <th>应还日期</th>
                    <th>还款金额</th>
                    <th>状态</th>
                </tr>
            </thead>
            <tbody>
                <tr v-if="dataList.loanRepayments.length" v-for="item in dataList.loanRepayments">
                    <td>{{ item.currentPeriod }}</td>
                    <td>{{ item.dueDate }}</td>
                    <td>{{ item.amountPrincipal + item.amountInterest }}</td>
                    <td>{{ item.status | repayStatus}}</td>
                </tr>
                <tr v-if="!dataList.loanRepayments.length">
                    <td colspan="4">暂无数据</td>
                </tr>
            </tbody>
        </table>
        <table v-if="navIndex===1">
            <thead>
                <tr>
                    <th>投资者</th>
                    <th>金额</th>
                    <th>投资时间</th>
                    <th>投资状态</th>
                </tr>
            </thead>
            <tbody>
                <tr v-if="dataList.invests.length" v-for="item in dataList.invests">
                    <td>{{ item.userName }}</td>
                    <td>{{ item.amount }}</td>
                    <td>{{ item.submitTime | date }}</td>
                    <td>{{ item.status | investStatus }}</td>
                </tr>
                <tr v-if="!dataList.invests.length">
                    <td colspan="4">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'bid-detail',
    props: {
        dataObj: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data () {
        return {
            dataList: {
                invests: [],
                loan: {},
                loanRepayments: [],
                product: {},
                projectDtail: {
                    project: {}
                }
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            navIndex: 0
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/loan/searchLoanByParams', {
                loan: {
                    id: this.dataObj.id
                }
            }, function (res) {
                if (res.data.message.code === 1000) {
                    This.dataList = res.data.loanDetail
                } else {
                    This.msg.content = res.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        goback () {
            this.$emit('backFn')
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.head span {
    display: inline-block;
    padding: 4px 20px;
    color: #333;
    font-weight: bold;
}
.goback {
    display: inline-block;
    padding: 4px 30px;
    color: #5ea7ea;
    cursor: pointer;
    font-size: 16px;
}
.bid-detail-p {
    line-height: 26px;
    padding: 0 6px;
    margin:10px 0;
    font-size: 16px;
    color:#333;
    font-weight: bold;
}
.bid-detail-p span {
    font-size: 14px;
    font-weight:normal;
    color:#666;
    word-wrap:break-word;
}
.nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .active {
        background-color: #75bcec;
        color: #fff;
    }
    li {
        display: block;
        padding: 6px 30px;
        font-size: 16px;
        cursor: pointer;
    }
}
.add-rate {
    color: #ff6f0a;
    font-size: 12px;
}
</style>



