<template>
<div class="merchantsToUser">
    <h3>商户向用户转账</h3>
    <div class="account-info">
        <div>
            平台账户：<selection :index-default="accountTypeIndex" :arr="accountTypeList" @selected="accountTypeFn"></selection>
        </div>
        <p class="padding">
            <span>可用余额：</span>
            <span>{{ accountInfo.balance }} 元</span>
        </p>
        <p class="padding">
            <span>冻结余额：</span>
            <span>{{ accountInfo.frozen_amount }} 元</span>
        </p>
    </div>
    
    <div class="to-user flex-style">
        <p>
            <label>转入用户：</label>
            <input v-model="mobile" type="text" placeholder="请输入用户手机号"/>
        </p>
        <!-- <a @click="chooseIdShow = !chooseIdShow" class="btn">添加用户</a> -->
    </div>
    <!-- <ul v-show="chooseIdShow" class="choose-id">
        <li class="flex-style">
            <p>
                <span>用户手机号：</span>
                <input type="text" placeholder="请输入用户手机号"/>
            </p>
            <span class="btn">查询</span>
        </li>
        <li>
            <span>真实姓名：</span>
            <span></span>
        </li>
        <li>
            <span>身份证号：</span>
            <span></span>
        </li>
        <li>
            <span>手机号码：</span>
            <span></span>
        </li>
        <li>
            <span>是否开户：</span>
            <span></span>
        </li>
        <li>
            <span>无密还款协议：</span>
            <span></span>
        </li>
        <li class="determine">
            <span class="btn">确定</span>
        </li>
    </ul> -->
    <div class="padding-top">
        原因类型：<selection :index-default="reasonIndex" :arr="reasonList" @selected="reasonFn"></selection>
    </div>
    <div class="padding-top">
        转入账户类型：<selection :index-default="transTypeIndex" :arr="transTypeList" @selected="transTypeFn"></selection>
    </div>
    <p>
        <label>转入金额：</label>
        <input v-model="transAmount" type="text" placeholder=""/>
        元
    </p>

    <p class="flex-style">
        <label>备注信息：</label>
        <textarea v-model="description" rows="3"></textarea>
    </p>
    <p>
        <a @click="sendData" class="btn submit">提交申请</a>
    </p>
    <h3>待审核的转账申请</h3>
    <table>
        <thead>
            <tr>
                <th>转出账户</th>
                <th>申请员工</th>
                <th>转入账号</th>
                <th>用户姓名</th>
                <th>转账金额</th>
                <th>状态</th>                  
                <th>申请时间</th>
                <th>原因类型</th>
                <th>转入账户类型</th>
                <th>备注</th>
                <th>操作</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ item.account }}</td>
                <td>{{ item.operationEmplyee }}</td>
                <td>{{ item.telephone }}</td>
                <td>{{ item.userName }}</td>
                <td>{{ item.tradeAmount }}</td>
                <td>审核中</td>                  
                <td>{{ item.strCreateTime }}</td>
                <td>{{ item.cause_type }}</td>
                <td>{{ item.charge_type }}</td>
                <td>{{ item.description }}</td>
                <td>
                    <a @click="cancleFn(item)" href="javascript:">取消</a>
                    <a @click="confirmFn(item, index)" href="javascript:">确认</a>
                </td>
            </tr>
        </tbody>
    </table>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'merchantsToUser',
    data () {
        return {
            accountInfo: {},
            list: [],
            accountTypeList: [
                {
                    lable: '自有子账户',
                    val: '1'
                },
                {
                    lable: '手续费子账户',
                    val: '3'
                },
                {
                    lable: '体验金子账户',
                    val: '4'
                },
                {
                    lable: '抵用金子账户',
                    val: '5'
                },
                {
                    lable: '加息金子账户',
                    val: '6'
                },
                {
                    lable: '保证金子账户',
                    val: '7'
                },
                {
                    lable: '奖励金子账户',
                    val: '9'
                },
                {
                    lable: '现金垫付子账户',
                    val: '10'
                }
            ],
            accountTypeIndex: 0,
            transTypeList: [
                {
                    lable: '个人现金投资账户',
                    val: '1'
                },
                {
                    lable: '个人现金融资账户',
                    val: '2'
                }
            ],
            transTypeIndex: 0,
            reasonList: [
                {
                    lable: '平台赠送',
                    val: '1'
                },
                {
                    lable: '平台补贴',
                    val: '2'
                },
                {
                    lable: '活动派发',
                    val: '3'
                },
                {
                    lable: '抵扣券未使用派发',
                    val: '4'
                },
                {
                    lable: '迁移存管转账',
                    val: '5'
                },
                {
                    lable: '加息补偿',
                    val: '6'
                }
            ],
            reasonIndex: 0,
            mobile: '',
            transAmount: '',
            description: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
            this.getList()
        })
    },
    watch: {
        accountTypeIndex: function (val, oldval) {
            this.getData()
        }
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/search/searchAccountBalance', {
                'account': '01',
                'acct_type': this.accountTypeList[this.accountTypeIndex].val
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.accountInfo = data.data.accountBalance
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        getList () {
            let This = this
            this.$ajax('/back/platform/platToPersonList', {}, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.appliactions
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        sendData () {
            let This = this
            if (!this.mobile || !this.transAmount) {
                if (!this.mobile) {
                    This.msg.content = '请输入转入用户'
                } else if (!this.transAmount) {
                    This.msg.content = '请输入转入金额'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/platform/platToPersonApply', {
                'platAccount': this.accountTypeList[this.accountTypeIndex].val,
                'telphone': this.mobile,
                'cause_type': this.reasonList[this.reasonIndex].val,
                'charge_type': this.transTypeList[this.transTypeIndex].val,
                'transAmount': this.transAmount,
                'description': this.description
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.getList()
                    This.mobile = ''
                    This.transAmount = ''
                    This.description = ''
                }
            }, function (err) {
                console.log(err)
            })
        },
        cancleFn (obj) {
            let This = this
            this.$ajax('/back/platform/platToPersonCancle', {
                id: obj.id
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.getList()
                }
            }, function (err) {
                console.log(err)
            })
        },
        confirmFn (obj, index) {
            let This = this
            this.$ajax('/back/platform/platToPersonAffirm', {
                id: obj.id
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.list.splice(index, 1)
                    This.getData()
                }
            }, function (err) {
                console.log(err)
            })
        },
        accountTypeFn (index) {
            this.accountTypeIndex = index
        },
        transTypeFn (index) {
            this.transTypeIndex = index
        },
        reasonFn (index) {
            this.reasonIndex = index
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.account-info {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.flex-style {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.btn {
    width: 100px;
    height: 36px;
    line-height: 36px;
    display: block;
    margin-left: 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
    user-select: none;
}
.submit {
    margin-left: 100px;
}
.choose-id {
    padding: 10px 30px;
    background-color: #ddd;
}
.determine {
    padding-top: 20px;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.padding {
    padding: 0 20px;
}
.padding-top {
    padding-top: 10px;
}
</style>



