// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import store from './store'

axios.defaults.withCredentials = true
let devUrl = '/Account_Manager_Back'

Vue.prototype.$get = function (url, success, err) {
	axios.get(devUrl + url)
	.then(function (response) {
		success && success(response)
	})
	.catch(function (error) {
		err && err(error)
	})
}

Vue.prototype.$ajax = function (url, obj, success, err) {
	axios.post(devUrl + url, obj)
	.then(function (response) {
		success && success(response)
		if (response.data.message.code === 2002) {
			router.push({path: 'login'})
		}
	})
	.catch(function (error) {
		err && err(error)
	})
}

Vue.prototype.checkMobile = function (s) {
	let reg = /^[1][34578][0-9]{9}$/
	return reg.test(s)
}

Vue.prototype.checkCard = function (s) {
	let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
	return reg.test(s)
}

Vue.filter('method', function(value) {
	if (value === 'MonthlyInterest') {
		return '按月付息到期还本'
	} else if (value === 'EqualInstallment') {
		return '按月等额本息'
	} else if (value === 'EqualPrincipal') {
		return '按月等额本金'
	} else if (value === 'BulletRepayment') {
		return '一次性还本付息'
	} else if (value === 'EqualInterest') {
		return '月平息'
	} else if (value === 'YearlyInterest') {
		return '按年付息到期还本'
	} else {
		return '按月付息到期还本'
	}
})

Vue.filter('status', function(value) {
	if (value === 'INITIATED') {
		return '初始'
	} else if (value === 'UNPLANED') {
		return '未安排'
	} else if (value === 'SCHEDULED') {
		return '已安排'
	} else if (value === 'OPENED') {
		return '开放投标'
	} else if (value === 'FAILED') {
		return '流标'
	} else if (value === 'FINISHED')  {
		return '已满标'
	} else if (value === 'CANCELED')  {
		return '已取消'
	} else if (value === 'SETTLED')  {
		return '已结算'
	} else if (value === 'CLEARED')  {
		return '已还清'
	} else if (value === 'OVERDUE')  {
		return '逾期'
	} else if (value === 'BREACH')  {
		return '违约'
	} else if (value === 'ARCHIVED')  {
		return '已存档'
	}
})

Vue.filter('repayStatus', function(value) {
	if (value === 'UNDUE') {
		return '未到期'
	} else if (value === 'OVERDUE') {
		return '逾期'
	} else if (value === 'BREACH') {
		return '违约'
	} else if (value === 'REPAYED') {
		return '已还请'
	} else if (value === 'PREPAY') {
		return '提前还款'
	}
})

Vue.filter('investStatus', function(value) {
	if (value === 'INITIALIZED') {
		return '初始'
	} else if (value === 'AUDITING') {
		return '待结算'
	} else if (value === 'FAILED') {
		return '失败'
	} else if (value === 'SETTLED') {
		return '已结算'
	} else if (value === 'CLEARED') {
		return '已还清'
	}
})

Vue.filter('date', function(value) {
	return new Date(parseInt(value, 10)).toLocaleDateString().split('/').join('-')
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	store,
	template: '<App/>',
	components: { App }
})



