import Vue from 'vue'
import Router from 'vue-router'

import login from '@/components/login/login'  // 登录

import rolePermissions from '@/components/staff/role-permissions/role-permissions'  // 角色权限
import staffList from '@/components/staff/list/list'   // 员工列表

import userList from '@/components/user/list/list'   // 用户列表
import reReferee from '@/components/user/re-referee/re-referee'   // 推荐人变更
import changeMobile from '@/components/user/change-mobile/change-mobile'   // 手机号变更
import userDetail from '@/components/user/detail/detail'   // 用户详情
import userInfo from '@/components/user/detail/info'   // 基本信息
import userCapital from '@/components/user/detail/capital'   // 资金明细
import userLend from '@/components/user/detail/lend'   // 借出历史
import userBorrow from '@/components/user/detail/borrow'   // 借入历史

import organization from '@/components/user/organization/organization'   // 机构管理

import product from '@/components/product/product'   // 产品管理
import project from '@/components/project/project'   // 项目管理

import bidList from '@/components/bid/list/list'   // 标的列表
import apply from '@/components/bid/apply/apply'   // 申请发标列表
import needDispatch from '@/components/bid/need-dispatch/need-dispatch'   // 需调度发标列表
import alreadyDispatch from '@/components/bid/already-dispatch/already-dispatch'   // 已调度发标列表
import ongoing from '@/components/bid/ongoing/ongoing'   // 进行中列表
import fullReview from '@/components/bid/full-review/full-review'   // 满标审核
import generateContract from '@/components/bid/generate-contract/generate-contract'   // 生成合同
import flow from '@/components/bid/flow/flow'   // 已流标列表
import cancle from '@/components/bid/cancle/cancle'   // 已取消列表

import repaymentToday from '@/components/repayment/today/today'   // 本日到期还款
import notDue from '@/components/repayment/not-due/not-due'   // 尚未到期还款
import payOff from '@/components/repayment/pay-off/pay-off'   // 已还清列表
import overDue from '@/components/repayment/over-due/over-due'   // 逾期未还列表
import breakContract from '@/components/repayment/break-contract/break-contract'   // 违约列表

import zjbdmx from '@/components/reconciliation/sub/zjbdmx'   // 运维查询--资金变动明细
import zjye from '@/components/reconciliation/sub/zjye'   // 运维查询--资金余额
import zjyemx from '@/components/reconciliation/sub/zjyemx'   // 运维查询--账户余额明细
import bdzhye from '@/components/reconciliation/sub/bdzhye'   // 运维查询--标的账户余额
import bdxx from '@/components/reconciliation/sub/bdxx'   // 运维查询--标的信息
import bdtzmx from '@/components/reconciliation/sub/bdtzmx'   // 运维查询--标的投资明细
import hkmx from '@/components/reconciliation/sub/hkmx'   // 运维查询--还款明细
import czdd from '@/components/reconciliation/sub/czdd'   // 运维查询--充值订单
import ddzt from '@/components/reconciliation/sub/ddzt'   // 运维查询--订单状态
import ptkhbh from '@/components/reconciliation/sub/ptkhbh'   // 运维查询--平台客户编号

import threePayment from '@/components/capital/three-payment/three-payment'   // 三方支付
import platformFund from '@/components/capital/platform-fund/platform-fund'   // 平台资金记录
import merchantsToUser from '@/components/capital/merchantsToUser/merchantsToUser'   // 商户向用户转账
import platChild from '@/components/capital/plat-child/plat-child'   // 平台不同子账户转账

import sInvitation from '@/components/statistics/invitation/invitation'   // 邀请统计

import cmsPic from '@/components/cms/pic/pic'   // 图片
import cmsArtical from '@/components/cms/artical/artical'   // 栏目文章
import newArtical from '@/components/cms/new-artical/new-artical'   // 新建文章
import column from '@/components/cms/column/column'   // 新建栏目

import mShort from '@/components/message/short/short'   // 短信管理
import mService from '@/components/message/service/service'   // 客服统计

import modifyPassword from '@/components/system/modify-password/modify-password'   // 修改密码

Vue.use(Router)

let rt = new Router({
  mode: 'history',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/role-permissions',
      component: rolePermissions
    },
    {
      path: '/staff-list',
      component: staffList
    },
    {
      path: '/user-list',
      component: userList
    },
    {
      path: '/user-detail',
      component: userDetail,
      redirect: '/user-detail/info',
      children: [
        {
          path: '/user-detail/info',
          component: userInfo
        },
        {
          path: '/user-detail/capital',
          component: userCapital
        },
        {
          path: '/user-detail/lend',
          component: userLend
        },
        {
          path: '/user-detail/borrow',
          component: userBorrow
        }
      ]
    },
    {
      path: '/organization',
      component: organization
    },
    {
      path: '/re-referee',
      component: reReferee
    },
    {
      path: '/change-mobile',
      component: changeMobile
    },
    {
      path: '/product',
      component: product
    },
    {
      path: '/project',
      component: project
    },
    {
      path: '/bid-list',
      component: bidList
    },
    {
      path: '/apply',
      component: apply
    },
    {
      path: '/need-dispatch',
      component: needDispatch
    },
    {
      path: '/already-dispatch',
      component: alreadyDispatch
    },
    {
      path: '/ongoing',
      component: ongoing
    },
    {
      path: '/full-review',
      component: fullReview
    },
    {
      path: '/generate-contract',
      component: generateContract
    },
    {
      path: '/flow',
      component: flow
    },
    {
      path: '/cancle',
      component: cancle
    },
    {
      path: '/repayment-today',
      component: repaymentToday
    },
    {
      path: '/not-due',
      component: notDue
    },
    {
      path: '/pay-off',
      component: payOff
    },
    {
      path: '/over-due',
      component: overDue
    },
    {
      path: '/break-contract',
      component: breakContract
    },
    {
      path: '/zjbdmx',
      component: zjbdmx
    },
    {
      path: '/zjye',
      component: zjye
    },
    {
      path: '/zjyemx',
      component: zjyemx
    },
    {
      path: '/bdzhye',
      component: bdzhye
    },
    {
      path: '/bdxx',
      component: bdxx
    },
    {
      path: '/bdtzmx',
      component: bdtzmx
    },
    {
      path: '/hkmx',
      component: hkmx
    },
    {
      path: '/czdd',
      component: czdd
    },
    {
      path: '/ddzt',
      component: ddzt
    },
    {
      path: '/ptkhbh',
      component: ptkhbh
    },
    {
      path: '/three-payment',
      component: threePayment
    },
    {
      path: '/platform-fund',
      component: platformFund
    },
    {
      path: '/merchants-to-user',
      component: merchantsToUser
    },
    {
      path: '/plat-child',
      component: platChild
    },
    {
      path: '/s-invitation',
      component: sInvitation
    },
    {
      path: '/cms-pic',
      component: cmsPic
    },
    {
      path: '/cms-artical',
      component: cmsArtical
    },
    {
      path: '/new-artical',
      component: newArtical
    },
    {
      path: '/column',
      component: column
    },
    {
      path: '/m-short',
      component: mShort
    },
    {
      path: '/m-service',
      component: mService
    },
    {
      path: '/modify-password',
      component: modifyPassword
    }
  ]
})

rt.beforeEach((to, from, next) => {
  next()
})

export default rt



