<template>
<div class="project-new">
    <span class="backpage" @click="goback">返回项目列表页</span class="back">
    <h3>{{ (flag === '1') ? '新建项目' : '编辑项目' }}</h3>
    <div class="row">
        <p class="dis">项目名称：</p>
        <input type="text" v-model="dataList.title" />
    </div>
    <div class="row">
        <p class="dis">唯一号：</p>
        <div>
            <input type="text" v-model="dataList.serial" />
            <span>例：DBS-20150831-021</span>
        </div>
        
    </div>
    <div class="row">
        <p class="dis">借款人：</p>
        <div>
            <input type="text" v-model="dataList.loanUser" />
            <span @click="queryUser.isShow = !queryUser.isShow" class="chooseUser">选择用户</span>
        </div>
    </div>
    <div v-show="queryUser.isShow" class="chooseUserBar">
        <div class="mid-row">
            <span class="dis">登陆名</span>
            <div>
                <input type="text" placeholder="请输入注册用户登陆名" v-model="queryUser.loginName" />
                <span @click="queryUserFn" class="chooseQuery">查询</span>
            </div>
        </div>
        <div class="mid-row">
            <span class="dis">真实姓名</span>
            <span>{{ queryUser.name }}</span>
        </div>
        <div class="mid-row">
            <span class="dis">身份证号</span>
            <span>{{ queryUser.id }}</span>
        </div>
        <div class="mid-row">
            <span class="dis">手机号码</span>
            <span>{{ queryUser.mobile }}</span>
        </div>
        <div>
            <span @click="queryUserEnd" class="btn">确定</span>
        </div>
    </div>
    <div class="row">
        <p class="dis">借款单位法人：</p>
        <input type="text" v-model="dataList.legalPerson" />
    </div>
    <div class="row">
        <p class="dis">借款单位代理人：</p>
        <input type="text" v-model="dataList.agentPerson" />
    </div>
    <div class="row">
        <p class="dis">关联企业：</p>
        <selection :index-default="companyIndex" keystr="shortname" :arr="companyList" @selected="companyFn"></selection>
    </div>
    <div class="row">
        <p class="dis">产品类型：</p>
        <selection :index-default="productIndex" keystr="name" :arr="productType" @selected="productTypeFn"></selection>
    </div>
    <div class="row">
        <p class="dis">项目总额（元）：</p>
        <input type="text" v-model="dataList.amount" />
    </div>
    <div v-if="(flag === '2')" class="row">
        <p class="dis">剩余可发标金额（元）：</p>
        <input type="text" v-model="dataList.surplusAmount" />
    </div>
    <div class="row">
        <p class="dis">借款期限：</p>
        <div>
            <input id="month" type="text" v-model="months" />
            <label for="month">月</label>
        </div>
    </div>
    <div class="row">
        <p class="dis">年利率：</p>
        <div>
            <input type="text" v-model="dataList.rate" />%
        </div>
    </div>
    <div class="row">
        <p class="dis">还款方式：</p>
        <selection :index-default="paymentIndex" :arr="paymentMethod" @selected="paymentFn"></selection>
    </div>
    <div class="row">
        <p class="dis">担保费用：</p>
        <p>
            <input type="text" v-model="dataList.loanGuaranteeFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis">借款管理率：</p>
        <p>
            <input type="text" v-model="dataList.loanManageFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis">服务费率：</p>
        <p>
            <input type="text" v-model="dataList.loanServiceFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis">还款（借款）利息管理率：</p>
        <p>
            <input type="text" v-model="dataList.loanInterestFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis">风险管理率：</p>
        <p>
            <input type="text" v-model="dataList.loanRiskFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis">回款（投资）利息管理率：</p>
        <p>
            <input type="text" v-model="dataList.investInterestFee" />%
        </p>
    </div>
    <div class="row">
        <p class="dis">公司简介：</p>
        <textarea cols="50" v-model="dataList.firmInfo"></textarea>
    </div>
    <div class="row">
        <p class="dis">经营范围：</p>
        <textarea cols="50" v-model="dataList.operationRange"></textarea>
    </div>
    <div class="row">
        <p class="dis">资金用途：</p>
        <textarea cols="50" v-model="dataList.description"></textarea>
    </div>
    <div class="row">
        <p class="dis">还款来源：</p>
        <textarea cols="50" v-model="dataList.repaySource"></textarea>
    </div>
    <div class="row">
        <p class="dis">风险措施：</p>
        <textarea cols="50" v-model="dataList.riskInfo"></textarea>
    </div>
    <div class="" >
        <div class="row">
            <p class="dis">法人身份证：</p>
            <div class="imgUpload">
                <input type="file" @change="onFileChange('cardImg', $event)" multiple>
                <span class="upload-btn choose-img">选择图片</span>
                <span @click="uploadImage('cardImg')" class="upload-btn">上传图片</span>
            </div>
        </div>
        <div class="photoBox">
            <ul>
                <li v-if="cardImg.upload.length" v-for="(item, index) in cardImg.upload">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('cardImg', item, '1')">X</i>
                </li>
                <li v-if="cardImg.callback.length && (flag === '2' || cardImg.fl === 1)" v-for="(item, index) in cardImg.callback">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('cardImg', item, '2')">X</i>
                </li>
            </ul>
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis">企业信息：</p>
            <div class="imgUpload">
                <input type="file" @change="onFileChange('companyImg', $event)" multiple>
                <span class="upload-btn choose-img">选择图片</span>
                <span @click="uploadImage('companyImg')" class="upload-btn">上传图片</span>
            </div>
        </div>
        <div class="photoBox">
            <ul>
                <li v-if="companyImg.upload.length" v-for="(item, index) in companyImg.upload">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('companyImg', item, '1')">X</i>
                </li>
                <li v-if="companyImg.callback.length && (flag === '2' || companyImg.fl === 1)" v-for="(item, index) in companyImg.callback">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('companyImg', item, '2')">X</i>
                </li>
            </ul>
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis">合同文件：</p>
            <div class="imgUpload">
                <input type="file" @change="onFileChange('contractImg', $event)" multiple>
                <span class="upload-btn choose-img">选择图片</span>
                <span @click="uploadImage('contractImg')" class="upload-btn">上传图片</span>
            </div>
        </div>
        <div class="photoBox">
            <ul>
                <li v-if="contractImg.upload.length" v-for="(item, index) in contractImg.upload">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('contractImg', item, '1')">X</i>
                </li>
                <li v-if="contractImg.callback.length && (flag === '2' || contractImg.fl === 1)" v-for="(item, index) in contractImg.callback">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('contractImg', item, '2')">X</i>
                </li>
            </ul>
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis">资产信息：</p>
            <div class="imgUpload">
                <input type="file" @change="onFileChange('assetsImg', $event)" multiple>
                <span class="upload-btn choose-img">选择图片</span>
                <span @click="uploadImage('assetsImg')" class="upload-btn">上传图片</span>
            </div>
        </div>
        <div class="photoBox">
            <ul>
                <li v-if="assetsImg.upload.length" v-for="(item, index) in assetsImg.upload">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('assetsImg', item, '1')">X</i>
                </li>
                <li v-if="assetsImg.callback.length && (flag === '2' || assetsImg.fl === 1)" v-for="(item, index) in assetsImg.callback">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('assetsImg', item, '2')">X</i>
                </li>
            </ul>
        </div>
    </div>
    <div class="">
        <div class="row">
            <p class="dis">其他资料：</p>
            <div class="imgUpload">
                <input type="file" @change="onFileChange('otherImg', $event)" multiple>
                <span class="upload-btn choose-img">选择图片</span>
                <span @click="uploadImage('otherImg')" class="upload-btn">上传图片</span>
            </div>
        </div>
        <div class="photoBox">
            <ul>
                <li v-if="otherImg.upload.length" v-for="(item, index) in otherImg.upload">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('otherImg', item, '1')">X</i>
                </li>
                <li v-if="otherImg.callback.length && (flag === '2' || otherImg.fl === 1)" v-for="(item, index) in otherImg.callback">
                    <img :src="[item]" alt="上传的图片">
                    <i @click="delImg('otherImg', item, '2')">X</i>
                </li>
            </ul>
        </div>
    </div>
    <p class="sub">
        <span class="resAll">重置</span>
        <span @click="saveFn" class="subAll">提交申请</span>
    </p>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../submodel/selection.vue'
import alert from '../submodel/alert.vue'
export default {
    name: 'project-new',
    props: {
        flag: {
            type: String,
            default: '1'
        },
        editData: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data () {
        return {
            companyList: [],
            companyIndex: 0,
            productType: [],
            productIndex: 0,
            paymentMethod: [
                {
                    lable: '按月付息到期还本',
                    val: 'MonthlyInterest'
                },
                {
                    lable: '按月等额本息',
                    val: 'EqualInstallment'
                },
                {
                    lable: '按月等额本金',
                    val: 'EqualPrincipal'
                },
                {
                    lable: '一次性还本付息',
                    val: 'BulletRepayment'
                },
                {
                    lable: '月平息',
                    val: 'EqualInterest'
                },
                {
                    lable: '按年付息到期还本',
                    val: 'YearlyInterest'
                }
            ],
            paymentIndex: 0,
            dataList: {
                id: '',    /* 项目id 编辑时用 */
                title: '',   /* 项目名称 */
                serial: '',   /* 唯一号 */
                loanUser: '',   /* 借款人 */
                loanUserId: '',   /* 借款人用户ID */
                legalPerson: '',   /* 借款单位法人 */
                agentPerson: '',   /* 借款单位代理人 */
                guaranteeRealm: '',   /* 关联企业 */
                guaranteeId: '',   /* 关联企业id */
                productId: '',   /* 产品类型 */
                amount: '',   /* 项目总额 */
                surplusAmount: '',   /* 剩余金额 */
                months: '',   /* 借款期限 */
                rate: '',   /* 年利率 */
                method: '',   /* 还款方式 */
                loanGuaranteeFee: '',   /* 担保费用 */
                loanManageFee: '',   /* 借款管理率 */
                loanServiceFee: '',   /* 服务费率 */
                loanInterestFee: '',   /* 还款（借款）利息管理率 */
                loanRiskFee: '',   /* 风险管理率 */
                investInterestFee: '',   /* 回款（投资）利息管理率 */
                firmInfo: '',   /* 公司简介 */
                operationRange: '',     /* 经营范围 */
                description: '',     /* 资金用途 */
                repaySource: '',     /* 还款来源 */
                riskInfo: ''     /* 风险措施 */
            },
            queryUser: {
                loginName: '',
                isShow: false,
                loanUserId: '',
                name: '',
                id: '',
                mobile: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            cardImg: {
                upload: [],
                fl: 0,
                callback: []
            },
            companyImg: {
                upload: [],
                fl: 0,
                callback: []
            },
            contractImg: {
                upload: [],
                fl: 0,
                callback: []
            },
            assetsImg: {
                upload: [],
                fl: 0,
                callback: []
            },
            otherImg: {
                upload: [],
                fl: 0,
                callback: []
            }
        }
    },
    created () {
        this.$nextTick(() => {
            let This = this
            this.$ajax('/back/cor/queryAllCorporationUserByCondition', {}, function (data) {
                This.companyList = data.data || []
                if (This.flag === '2') {
                    This.companyIndex = This.getIndex(This.editData.guaranteeId, This.companyList, 'userId')
                }
            }, function (err) {
                console.log(err)
            })
            this.$ajax('/back/product/queryAllProducts', {}, function (data) {
                This.productType = data.data.products || []
                if (This.flag === '2') {
                    This.productIndex = This.getIndex(This.editData.productId, This.productType, 'id')
                }
            }, function (err) {
                console.log(err)
            })
            if (this.flag === '2') {
                this.dataList.id = this.editData.id
                this.dataList.title = this.editData.title
                this.dataList.serial = this.editData.serial
                this.dataList.loanUser = this.editData.name || ''
                this.dataList.loanUserId = this.editData.loanUserId
                this.dataList.legalPerson = this.editData.legalPerson
                this.dataList.agentPerson = this.editData.agentPerson
                this.dataList.guaranteeRealm = this.editData.guaranteeRealm
                this.dataList.guaranteeId = this.editData.guaranteeId
                this.dataList.productId = this.editData.productId
                this.dataList.amount = this.editData.amount
                this.dataList.surplusAmount = this.editData.surplusAmount
                this.dataList.rate = this.editData.rate
                this.dataList.method = this.editData.method
                this.dataList.loanGuaranteeFee = this.editData.loanGuaranteeFee
                this.dataList.loanManageFee = this.editData.loanManageFee
                this.dataList.loanServiceFee = this.editData.loanServiceFee
                this.dataList.loanInterestFee = this.editData.loanInterestFee
                this.dataList.loanRiskFee = this.editData.loanRiskFee
                this.dataList.investInterestFee = this.editData.investInterestFee
                this.dataList.firmInfo = this.editData.firmInfo
                this.dataList.operationRange = this.editData.operationRange
                this.dataList.description = this.editData.description
                this.dataList.repaySource = this.editData.repaySource
                this.dataList.riskInfo = this.editData.riskInfo
                this.cardImg.callback = this.editData.legalPersonPhotoUrl ? this.editData.legalPersonPhotoUrl.split(',') : []
                this.companyImg.callback = this.editData.enterpriseInfoPhotoUrl ? this.editData.enterpriseInfoPhotoUrl.split(',') : []
                this.contractImg.callback = this.editData.contractPhotoUrl ? this.editData.contractPhotoUrl.split(',') : []
                this.assetsImg.callback = this.editData.assetsPhotoUrl ? this.editData.assetsPhotoUrl.split(',') : []
                this.otherImg.callback = this.editData.othersPhotoUrl ? this.editData.othersPhotoUrl.split(',') : []
                this.paymentIndex = this.getIndex(This.dataList.method, This.paymentMethod, 'val')
            }
        })
    },
    computed: {
        months: function () {
            return this.productType.length <= 0 ? '' : this.productType[this.productIndex].months
        }
    },
    components: {
        selection,
        alert
    },
    methods: {
        onFileChange (str, e) {
            let files = e.target.files || e.dataTransfer.files
            if (!files.length) return
            this.createImage(files, str)
        },
        createImage (file, str) {
            if (typeof FileReader === 'undefined') {
                alert('您的浏览器不支持图片上传，请升级您的浏览器')
                return false
            }
            var This = this
            var leng = file.length
            for (var i = 0; i < leng; i++) {
                var reader = new FileReader()
                reader.readAsDataURL(file[i])
                reader.onload = function (e) {
                    This[str].upload.push(e.target.result)
                }
            }
        },
        uploadImage (str) {
            if (this[str].upload.length <= 0) {
                this.msg.content = '没有可上传的图片，如需上传图片，请先点击选择图片'
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            let This = this
            this.$ajax('/back/upload/uploadPhoto', {
                files: this[str].upload,
                type: 'upload'
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This[str].upload = []
                    data.data.path.forEach((item) => {
                        This[str].callback.push(item)
                    })
                    if (This.flag === '1') {
                        This[str].fl = 1
                    }
                }
            }, function (err) {
                console.log(err)
            })
        },
        delImg (str, obj, num) {
            let This = this
            if (num === '1') {
                this[str].upload.forEach((item, index, array) => {
                    if (item === obj) {
                        array.splice(index, 1)
                    }
                })
            } else if (num === '2') {
                this.$ajax('/back/upload/deletePhoto', {
                    type: 'upload',
                    url: obj
                }, function (data) {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                    if (data.data.message.code === 1000) {
                        This[str].callback.forEach((item, index, array) => {
                            if (item === obj) {
                                array.splice(index, 1)
                            }
                        })
                    }
                }, function (err) {
                    console.log(err)
                })
            }
        },
        companyFn (index) {
            this.companyIndex = index
        },
        productTypeFn (index) {
            this.productIndex = index
        },
        paymentFn (index) {
            this.paymentIndex = index
        },
        goback () {
            this.$emit('back')
        },
        queryUserFn () {
            let This = this
            if (!this.queryUser.loginName) {
                this.msg.content = '登陆名不能为空'
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            this.$ajax('/back/user/searchUserByParams', {
                user: {
                   'loginName': this.queryUser.loginName
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.queryUser.loanUserId = data.data.user.id
                    This.queryUser.name = data.data.user.userName
                    This.queryUser.id = data.data.user.idCode
                    This.queryUser.mobile = data.data.user.mobile
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        queryUserEnd () {
            if (this.queryUser.loanUserId) {
                this.dataList.loanUserId = this.queryUser.loanUserId
                this.dataList.loanUser = this.queryUser.name
                this.queryUser.isShow = false
            }
        },
        saveFn () {
            let saveUrl = (this.flag === '2') ? '/back/project/modifyProject' : '/back/project/projectApply'
            let This = this
            let reg = /^\+?[1-9][0-9]*$/
            if (!this.dataList.title || !this.dataList.serial || !this.dataList.loanUserId || (!this.dataList.legalPerson && !this.dataList.agentPerson) || !this.dataList.amount || !this.dataList.rate || !reg.test(this.dataList.amount) || !this.companyList[this.companyIndex] || !this.productType[this.productIndex] || !this.paymentMethod[this.paymentIndex]) {
                if (!this.dataList.title) {
                    this.msg.content = '项目名称不能为空'
                } else if (!this.dataList.serial) {
                    this.msg.content = '唯一号不能为空'
                } else if (!this.dataList.loanUserId) {
                    this.msg.content = '借款人不能为空'
                } else if ((!this.dataList.legalPerson && !this.dataList.agentPerson)) {
                    this.msg.content = '借款单位法人或借款单位代理人不能为空'
                } else if (!this.dataList.amount) {
                    this.msg.content = '项目总额不能为空'
                } else if (!this.dataList.rate) {
                    this.msg.content = '年利率不能为空'
                } else if (!reg.test(this.dataList.amount)) {
                    this.msg.content = '项目总额请输入一个正整数'
                } else if (!this.companyList[this.companyIndex]) {
                    this.msg.content = '关联企业不能为空'
                } else if (!this.productType[this.productIndex]) {
                    this.msg.content = '产品类型不能为空'
                } else if (!this.paymentMethod[this.paymentIndex]) {
                    this.msg.content = '还款方式不能为空'
                }
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            this.$ajax(saveUrl, {
                project: {
                    'id': this.dataList.id,
                    'title': this.dataList.title,
                    'serial': this.dataList.serial,
                    'loanUserId': this.dataList.loanUserId,
                    'legalPerson': this.dataList.legalPerson,
                    'agentPerson': this.dataList.agentPerson,
                    'guaranteeRealm': this.companyList[this.companyIndex].shortname,
                    'guaranteeId': this.companyList[this.companyIndex].userId,
                    'productId': this.productType[this.productIndex].id,
                    'amount': this.dataList.amount,
                    'surplusAmount': this.dataList.surplusAmount,
                    'months': this.months,
                    'rate': this.dataList.rate,
                    'method': this.paymentMethod[this.paymentIndex].val,
                    'loanGuaranteeFee': this.dataList.loanGuaranteeFee,
                    'loanManageFee': this.dataList.loanManageFee,
                    'loanServiceFee': this.dataList.loanServiceFee,
                    'loanInterestFee': this.dataList.loanInterestFee,
                    'loanRiskFee': this.dataList.loanRiskFee,
                    'investInterestFee': this.dataList.investInterestFee,
                    'firmInfo': this.dataList.firmInfo,
                    'operationRange': this.dataList.operationRange,
                    'description': this.dataList.description,
                    'repaySource': this.dataList.repaySource,
                    'riskInfo': this.dataList.riskInfo
                },
                'legalPersonPhoto': this.cardImg.callback,
                'enterpriseInfoPhoto': this.companyImg.callback,
                'contractPhoto': this.contractImg.callback,
                'assetsPhoto': this.assetsImg.callback,
                'othersPhoto': this.otherImg.callback
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    setTimeout(() => {
                        This.$emit('back')
                    }, 1500)
                }
            }, function (err) {
                console.log(err)
            })
        },
        getIndex (val, arr, key) {
            let i = 0
            arr.forEach(function (item, index) {
                if (item[key] === val) {
                    i = index
                }
            })
            return i
        },
        closeMsg () {
            this.msg.isShow = false
        }
    }
}
</script>

