<template>
<div class="selection">
    <div @click="tabShow = !tabShow" class="selected">{{ (arr[0] && arr[0][keystr])?arr[showIndex][keystr]:'' }}<i></i></div>
    <ul v-show="tabShow">
        <li v-if="arr" @click="tabFn(index)" v-for="(item, index) in arr">{{ item[keystr]?item[keystr]:'' }}</li>
    </ul>
</div>
</template>

<script>
export default {
    name: 'selection',
    props: {
        arr: {
            type: Array,
            default: []
        },
        indexDefault: {
            type: Number,
            default: 0
        },
        keystr: {
            type: String,
            default: 'lable'
        }
    },
    data () {
        return {
            tabShow: false,
            showIndex: 0
        }
    },
    watch: {
        indexDefault: function (val, oldval) {
            this.showIndex = val
        }
    },
    methods: {
        tabFn (index) {
            this.tabShow = false
            this.showIndex = index
            this.$emit('selected', index)
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.selection {
    position: relative;
    display: inline-block;
    margin-right: 10px;
    ul {
        position: absolute;
        left: 0;
        top: 30px;
        z-index: 2;
        border: 1px solid #ddd;
        background-color: #e2e2e2;
        li {
            white-space: nowrap;
            padding: 0 26px 0 12px;
            line-height: 30px;
            cursor: pointer;
            &:hover {
                background-color: #7fcbff;
            }
        }
    }
}
.selected {
    display: inline-block;
    height: 28px;
    line-height: 28px;
    padding: 0 12px;
    border: 1px solid #ddd;
    cursor: pointer;
    i {
        display: inline-block;
        width: 0;
        height: 0;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 10px solid #666;
        margin-left: 4px;
    }
}
</style>



