<template>
<div @click="treeShow=true" class="tree-selection">
    <span>{{ treeTitle }}</span>
    <input @click.stop="treeShow=!treeShow" v-model="obj.treeSelection" type="text">
    <tree @choosed="chooseFn" class="list" :class="{dis: treeShow}" :list="list"></tree>
</div>
</template>

<script>
import tree from './tree.vue'
export default {
    name: 'tree-selection',
    props: {
        list: {
            type: Array,
            default: function () {
                return []
            }
        },
        treeTitle: {
            type: String,
            default: ''
        },
        defaultData: {
            type: Object,
            default: function () {
                return {
                    treeSelection: '',
                    treeId: ''
                }
            }
        }
    },
    data () {
        return {
            obj: {
                treeSelection: '',
                treeId: ''
            },
            treeShow: true
        }
    },
    watch: {
        obj: {
            handler: function (val, oldval) {
                this.$emit('treeChange', val)
            },
            deep: true
        },
        defaultData: {
            handler: function (val, oldval) {
                if (val.treeSelection) {
                    this.obj.treeSelection = val.treeSelection
                    this.obj.treeId = val.treeId
                }
            },
            deep: true
        }
    },
    methods: {
        chooseFn (id, name) {
            this.obj.treeSelection = name
            this.obj.id = id
            this.treeShow = true
        }
    },
    components: {
        tree
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tree-selection {
    display: inline-block;
    position: relative;
    line-height: 36px;
}
.list {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
}
.dis {
    display: none;
}
input {
    line-height: 34px;
    border: 1px solid #ddd;
    padding: 0 6px;
}
</style>



