<template>
<div class="platform-fund">
    <div class="head">
        <h4>往来账户</h4>
        <p>
            可用余额：<span>{{ accountInfo.balance }} 元</span>
        </p>
        <p>
            冻结余额：<span>{{ accountInfo.frozen_amount }} 元</span>
        </p>
    </div>
    <div class="search-bar">
        <div>
            <span class="select-lable">用户手机号：</span>
            <input v-model="parame.mobile" class="searchBid" type="text">
        </div>
        &nbsp;&nbsp;
        <div>
            <span class="select-lable">资金类型：</span>
            <selection :index-default="typeIndex" :arr="typeList" @selected="selectionCallback"></selection>
        </div>
        <v-search @searchFn="searchFn" @changeVal="searchValChange"></v-search>
        <span class='export-btn'>导出</span>
    </div>
    <table>
        <thead>
            <tr>
                <th>序号</th>
                <th>订单号</th>
                <th>账户</th>
                <th>用户名</th>
                <th>真实姓名</th>
                <th>类型</th>
                <th>操作</th>
                <th>金额(元)</th>
                <th>状态</th>
                <th>日期</th>
                <th>备注</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ (parame.now-1)*10+index+1 }}</td>
                <td>{{ item.orderId }}</td>
                <td>{{ item.account }}</td>
                <td>{{ item.mobile }}</td>
                <td>{{ item.userName }}</td>
                <td>{{ item.strType }}</td>
                <td>{{ (item.operation === "OUT"?"转出":"转入") }}</td>
                <td>{{ item.amount }}</td>
                <td>{{ item.strStatus }}</td>
                <td>{{ item.strTimeRecorded }}</td>
                <td>{{ item.description }}</td>
            </tr>
            <tr v-if="!list.length && parame.isFinished">
                <td colspan="11">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
import pagination from '../../submodel/pagination.vue'
export default {
    name: 'platform-fund',
    data () {
        return {
            accountInfo: {},
            list: [],
            typeList: [
                {
                    lable: '全部',
                    val: ''
                },
                {
                    lable: '充值',
                    val: 'DEPOSIT'
                },
                {
                    lable: '提现',
                    val: 'WITHDRAW'
                },
                {
                    lable: '放款',
                    val: 'LOAN'
                },
                {
                    lable: '贷款还款',
                    val: 'LOAN_REPAY'
                },
                {
                    lable: '流标返款',
                    val: 'FAILED_LOAN_REPAY'
                },
                {
                    lable: '垫付还款',
                    val: 'DISBURSE'
                },
                {
                    lable: '投资还款',
                    val: 'INVEST_REPAY'
                },
                {
                    lable: '平台转入',
                    val: 'TRANSFERIN'
                },
                {
                    lable: '平台转出',
                    val: 'TRANSFEROUT'
                },
                {
                    lable: '平台不同子账户之间转账',
                    val: 'PLAT_TRANS'
                },
                {
                    lable: '提现手续费',
                    val: 'FEE_WITHDRAW'
                },
                {
                    lable: '身份验证手续费',
                    val: 'FEE_AUTHENTICATE'
                },
                {
                    lable: '投资管理费',
                    val: 'FEE_INVEST_INTEREST'
                },
                {
                    lable: '借款服务费',
                    val: 'FEE_LOAN_SERVICE'
                },
                {
                    lable: '借款管理费',
                    val: 'FEE_LOAN_MANAGE'
                },
                {
                    lable: '还款管理费',
                    val: 'FEE_LOAN_INTEREST'
                },
                {
                    lable: '担保费',
                    val: 'FEE_LOAN_GUARANTEE'
                },
                {
                    lable: '风险管理费',
                    val: 'FEE_LOAN_RISK'
                },
                {
                    lable: '充值手续费',
                    val: 'FEE_DEPOSIT'
                }
            ],
            typeIndex: 0,
            parame: {
                now: 1,
                all: 1,
                mobile: '',
                startTime: '',
                endTime: '',
                isFinished: false
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getAccountInfo()
            this.getDataList()
        })
    },
    methods: {
        getAccountInfo () {
            let This = this
            this.$ajax('/back/search/searchAccountInfo', {
                'account': '01'
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.accountInfo = data.data.accountInfo || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        getDataList () {
            let This = this
            this.$ajax('/back/platForm/searchPlatFormRecord', {
                page: {
                    strStartTime: this.parame.startTime,
                    strEndTime: this.parame.endTime,
                    pageNo: this.parame.now,
                    params: {
                        mobile: this.parame.mobile,
                        type: this.typeList[this.typeIndex].val
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results || []
                    This.parame.all = data.data.page.totalPage
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        searchFn () {
            this.getDataList()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        selectionCallback (index) {
            this.typeIndex = index
        },
        pagingCallback (index) {
            this.parame.now = index
            this.getDataList()
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        'v-search': search,
        selection,
        alert,
        pagination
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.head {
    width: 400px;
    margin-left: 20px;
    border: 1px solid #e2e2e2;
    h4 {
        height: 36px;
        padding: 0 14px;
        margin: 0;
        line-height: 36px;
        color: #fff;
        background-color: #8ed1ff;
    }
    p {
        padding-left: 40px;
        span {
            color: red;
        }
    }
}
.search-btn, .export-btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.export-btn {
    background-color: #ff5825;
}
.search-bar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.setColor {
    color: #fff;
    background-color: green !important;
}
</style>



