<template>
<div class="re-referee">
    <div>
        <h4>单个客户变更</h4>
        <span>变更人手机号：</span>
        <input v-model="userMobile" type="text" value="" placeholder="变更人手机号"/> -- 
        <span>添加到：</span>
        <input v-model="refMobile" type="text" value="" placeholder="推荐人手机号"/>
        <span @click="modifyReferral" class="btn">确定</span>
    </div>
    <div class="marginTop">
        <h4>批量客户变更</h4>
        <span>变更人手机号：</span>
        <input v-model="oriMobile" type="text" value="" placeholder="原推荐人手机号"/> -- 
        <span>添加到：</span>
        <input v-model="nowMobile" type="text" value="" placeholder="推荐人手机号"/>
        <span @click="batchModifyReferral" class="btn">确定</span>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 're-referee',
    data () {
        return {
            userMobile: '',
            refMobile: '',
            oriMobile: '',
            nowMobile: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    components: {
        alert
    },
    methods: {
        modifyReferral () {
            let This = this
            this.$ajax('/back/user/modifyReferral', {
                'userMobile': this.userMobile,
                'refMobile': this.refMobile
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        batchModifyReferral () {
            let This = this
            this.$ajax('/back/user/batchModifyReferral', {
                'oriMobile': this.oriMobile,
                'nowMobile': this.nowMobile
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input {
    border: 1px solid #ddd;
    padding: 6px 10px;
}
.marginTop {
    margin-top: 20px;
    border-top: 1px solid #ddd;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
</style>



