<template>
<div class="zjye">
    <p>
        账号：<input v-model="account" type="text" placeholder="客户手机号或平台编号" size="30"/>
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>可用余额（元）</th>
                    <th>冻结金额（元）</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{{ list.balance }}</td>
                    <td>{{ list.frozen_amount }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'zjye',
    data () {
        return {
            list: {},
            account: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.account) {
                This.msg.content = '请输入客户手机号或平台编号'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchAccountInfo', {
                'account': this.account
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.accountInfo || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



