<template>
<div class="employee-list">
    <h3>员工列表</h3>
    <div class="head">
        <tree-selection @treeChange="treeCallback" ref="treeselection" :list="treeDate" :tree-title="treeTitle"></tree-selection>
        <a @click="newStaffShow = true" href="javascript:">添加员工</a>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>姓名</th>
                    <th>登录名</th>
                    <th>手机号码</th>
                    <th>注册时间</th>
                    <th>最后登录时间</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in dataList">
                    <td><a @click="edit(index)" href="javascript:void(0);">{{ item.empName }}</a></td>
                    <td>{{ item.loginName }}</td>
                    <td>{{ item.mobile }}</td>
                    <td>{{ item.regDate }}</td>
                    <td>{{ item.lastDate }}</td>
                    <td><a @click="edit(index)" href='javascript:void(0);'>编辑</a><a @click="disable(item.id, index)" href='javascript:void(0);'>禁用</a></td>
                </tr>
                <tr v-if="!dataList.length">
                    <td colspan="6">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <pagination v-if="all>1" class="paging" @pagingFn="pagingCallback" :now="now" :all="all"></pagination>
    <div v-if="newStaffShow" class="add-wrap">
        <div class="add-content">
            <h4>添加员工</h4>
            <i @click="newStaffShow = false">X</i>
            <div class="row">
                <p>登录名：</p>
                <input v-model="newStaff.loginName" type="text" />
            </div>
            <div class="row">
                <p>姓名：</p>
                <input v-model="newStaff.name" type="text" />
            </div>
            <div class="row">
                <p>身份证号：</p>
                <input v-model="newStaff.idnumber" type="text" />
            </div>
            <div class="row">
                <p>手机号码：</p>
                <input v-model="newStaff.mobile" type="text" />
            </div>
            <tree-selection class="tree1" @treeChange="treeCallback1" ref="treeselection1" :list="treeDate" :tree-title="treeTitle1"></tree-selection>
            <div class="btn">
                <span @click="addStaffFn">确定</span>
                <span @click="newStaffShow = false">取消</span>
            </div>
        </div>
    </div>
    <alert class="edit-staff" @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames">
        <template slot="content">
            <div class="row">
                <p>登录名：</p>
                <span class="detail-data">{{ editData.loginName }}</span>
            </div>
            <div class="row">
                <p>手机号码：</p>
                <span class="detail-data">{{ editData.mobile }}</span>
            </div>
            <div class="row">
                <p>身份证号码：</p>
                <span class="detail-data">{{ editData.idnumber }}</span>
            </div>
            <div class="row">
                <p>最后登录：</p>
                <span class="detail-data">{{ editData.lastDate }}</span>
            </div>
            <div class="row">
                <p>系统角色：</p>
                <div class="to-line">
                    <p v-for="(item, index) in roles">
                        <input type="checkbox" :id="item.id" :value="item.id" v-model="rolesList" />
                        <label :for="item.id">{{item.r_name}}</label>
                    </p>
                </div>
            </div>
            <tree-selection class="tree2" @treeChange="treeCallback2" ref="treeselection2" :defaultData="editData" :list="treeDate" :tree-title="treeTitle1"></tree-selection>
        </template>
    </alert>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import treeSelection from '../../submodel/tree-selection.vue'
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'employee-list',
    data () {
        return {
            roles: [],
            rolesList: [],
            treeDate: [],
            treeTitle: '机构：',
            treeTitle1: '所属机构：',
            jigou: '',
            dataList: [],
            dataIndex: 0,
            now: 1,
            all: 1,
            newStaffShow: false,
            newStaff: {
                loginName: '',
                name: '',
                idnumber: '',
                mobile: '',
                mechanis: ''
            },
            editData: {
                treeSelection: '',
                treeId: '',
                loginName: '',
                mobile: '',
                idnumber: '',
                lastDate: '',
                mechanis: ''
            },
            alertParames: {
                isShow: false,
                width: 500,
                showTitle: true,
                title: '员工详情/编辑',
                content: '1',
                cancel: '取消',
                makesure: '确定'
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getStaffList()
            this.getJigou()
        })
    },
    methods: {
        treeCallback1 (val) {
            this.newStaff.mechanis = val.id
        },
        treeCallback (val) {
            this.jigou = val.id
            this.getStaffList()
        },
        treeCallback2 (val) {
            this.editData.mechanis = val.id
        },
        getStaffList () {
            var This = this
            this.$ajax('/back/emp/empList', {
                'pageNo': this.now,
                'pageSize': 10,
                'params': {
                    'orgId': this.jigou
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.dataList = data.data.page.results
                    This.all = data.data.page.totalPage
                    This.roles = data.data.roles
                } else {
                    This.dataList = []
                }
            }, function (err) {
                console.log(err)
            })
        },
        getJigou () {
            var This = this
            this.$ajax('/back/org/queryOrganizationByParams', {
                'level': 0
            }, function (data) {
                This.treeDate = data.data.list
            }, function (err) {
                console.log(err)
            })
        },
        pagingCallback (num) {
            this.now = num
            this.getStaffList()
        },
        disable (id, index) {
            var This = this
            this.$ajax('/back/emp/empModifyInfo', {
                emp: {
                    id: id,
                    enabled: false
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.dataList.splice(index, 1)
                }
            }, function (err) {
                console.log(err)
            })
        },
        addStaffFn () {
            var This = this
            if (this.newStaff.loginName === '' || !this.checkMobile(this.newStaff.mobile) || !this.checkCard(this.newStaff.idnumber)) {
                if (this.newStaff.loginName === '') {
                    This.msg.content = '登录名不能为空'
                }
                if (!this.checkMobile(this.newStaff.mobile)) {
                    This.msg.content = '请输入正确的手机号'
                }
                if (!this.checkCard(this.newStaff.idnumber)) {
                    This.msg.content = '请输入正确的身份证号'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/emp/addEmployee', {
                emp: {
                    idnumber: this.newStaff.idnumber,
                    loginName: this.newStaff.loginName,
                    empName: this.newStaff.name,
                    mobile: this.newStaff.mobile,
                    orgId: this.newStaff.mechanis
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.newStaffShow = false
                }
            }, function (err) {
                console.log(err)
            })
        },
        getChecked () {
            let obj = this.dataList[this.dataIndex]
            this.rolesList = []
            if (obj.roles.length > 0) {
                for (let i = 0, len = obj.roles.length; i < len; i++) {
                   this.rolesList.push(obj.roles[i].id)
                }
            }
        },
        edit (index) {
            this.dataIndex = index
            this.alertParames.isShow = true
            this.editData.loginName = this.dataList[index].loginName
            this.editData.mobile = this.dataList[index].mobile
            this.editData.idnumber = this.dataList[index].idnumber
            this.editData.lastDate = this.dataList[index].lastDate
            this.editData.treeSelection = this.dataList[index].orgName || ''
            this.editData.treeId = this.dataList[index].orgId || ''
            this.getChecked()
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
           this.alertParames.isShow = false
        },
        makesureAlert () {
            let This = this
            this.$ajax('/back/emp/dispatchEmpRole', {
                emp: {
                    id: this.dataList[this.dataIndex].id,
                    orgId: this.editData.mechanis
                },
                roleIds: this.rolesList
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        treeSelection,
        pagination,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.head {
    padding: 4px 0;
    display: flex;
    justify-content: space-between;
    a {
        display: block;
        padding: 6px 12px;
        font-size: 18px;
        color: #38afff;
        &:hover {
            text-decoration: underline;
        }
    }
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.add-wrap {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    text-align: center;
    &:before {
        content: '';
        height: 100%;
        display: inline-block;
        vertical-align: middle;
    }
    .add-content {
        position: relative;
        padding-bottom: 10px;
        background: #fff;
        display: inline-block;
        vertical-align: middle;
        h4 {
            height: 50px;
            margin: 0;
            background-color: #75bcec;
            color: #fff;
            font-size: 16px;
            text-align: center;
            line-height: 50px;
        }
        i {
            position: absolute;
            font-style: normal;
            top: 8px;
            right: 8px;
            width: 20px;
            height: 20px;
            line-height: 20px;
            text-align: center;
            font-weight: 400;
            font-size: 18px;
            color: #fff;
            cursor: pointer;
            border-radius: 50%;
        }
    }
}
.tree1 {
    left: -24px;
}
.tree2 {
    left: 84px;
}
.edit-staff {
    .row {
        width: 400px;
        margin: 10px auto;
        p {
            width: 120px;
        }
    }
}
.row {
    width: 540px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    p {
        text-align: right;
        width: 200px;
    }
}
.btn {
    padding-top: 10px;
    text-align: center;
    span {
        display: inline-block;
        padding: 6px 20px;
        background-color: #289cff;
        height: 20px;
        border-radius: 4px;
        color: #fff;
        cursor: pointer;
        border: 0;
        &:hover {
            text-decoration: underline;
        }
    }
}
input[type="text"], .detail-data {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.to-line {
    display: flex;
    width: 440px;
    justify-content: flex-start;
    flex-wrap: wrap;
    p {
        flex: auto;
        padding: 0 12px;
        user-select: none;
    }
}
</style>



