<template>
<div class="hkmx">
    <p>
        手机号：<input v-model="parame.mobile" type="text" placeholder="必填" size="20"/>&nbsp;&nbsp;
        查询类型：<selection class="inline-block" :index-default="queryTypeIndex" :arr="queryTypeList" @selected="queryTypeFn"></selection>
        标的ID：<input v-model="parame.prodId" type="text" placeholder="选填" size="20"/>&nbsp;&nbsp;
        页码：<input v-model="parame.now" type="text" placeholder="必填" size="2">&nbsp;&nbsp;
        <v-search class="inline-block" @searchFn="searchFn" @changeVal="searchValChange"></v-search>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>平台编号</th>
                    <th>标的名称</th>
                    <th>还款期数</th>
                    <th>计划还款金额（元）</th>
                    <th>实际还款金额（元）</th>
                    <th>计划还款时间</th>
                    <th>实际还款时间</th>
                    <th>状态</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in obj">
                    <td>{{ item.plat_no }}</td>
                    <td>{{ item.prod_name }}</td>
                    <td>{{ item.repay_num }}</td>
                    <td>{{ item.repay_amt }}</td>
                    <td>{{ item.real_repay_amt }}</td>
                    <td>{{ item.repay_date }}</td>
                    <td>{{ item.real_repay_date }}</td>
                    <td>{{ item.status }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'hkmx',
    data () {
        return {
            obj: [],
            queryTypeIndex: 0,
            queryTypeList: [
                {
                    lable: '投资人收款记录',
                    val: 1
                },
                {
                    lable: '借款人还款记录',
                    val: 2
                }
            ],
            parame: {
                now: 1,
                startTime: '',
                endTime: '',
                prodId: '',
                mobile: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.checkMobile(this.parame.mobile) || !this.parame.now) {
                if (!this.checkMobile(this.parame.mobile)) {
                    This.msg.content = '请输入正确手机号'
                } else if (!this.parame.now) {
                    This.msg.content = '请输入页码'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchRepayDetail', {
                'mobile': this.parame.mobile,
                'type': this.queryTypeList[this.queryTypeIndex].val,
                'prod_id': this.parame.prodId,
                'start_date': this.parame.startTime,
                'end_date': this.parame.endTime,
                'pagenum': this.parame.now
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.repayDetails || []
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        queryTypeFn (index) {
            this.queryTypeIndex = index
        },
        searchFn () {
            this.getData()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        'v-search': search,
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.inline-block {
    display: inline-block;
}
</style>



