<template>
<div class="organization">
	<tree v-if="treeList.orgName" ref="tree" :data="treeList" :node-text="nodeText" :zoomable="true" :margin-x="30" :margin-y="30" class="tree" @clicked="onClick"></tree>
	<add-org @closePopup="closeAddOrg" v-if="addOrgShow" :obj="editeData"></add-org>
</div>
</template>

<script>
import {tree} from 'vued3tree'
import addOrg from './add.vue'
export default {
    name: 'organization',
	data () {
	    return {
	    	treeList: {},
	        nodeText: 'orgName',
	        addOrgShow: false,
	        editeData: {}
	    }
	},
	components: {
	    tree,
	    addOrg
	},
	created () {
		this.$nextTick(() => {
			this.getData()
		})
	},
	methods: {
		getData () {
			let This = this
            this.$ajax('/back/org/queryAllOrganizations', {}, function (data) {
            	This.treeList = data.data
            }, function (err) {
                console.log(err)
            })
		},
		onClick (evt) {
			this.addOrgShow = true
			this.editeData = evt.data
	    },
	    closeAddOrg (val) {
	    	this.addOrgShow = false
	    	if (val === 'b') {
	    		this.getData()
	    	}
	    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.organization {
	position: relative;
}
.tree {
  	height: 600px;
  	width: 100%;
}
</style>



