<template>
<div v-show="parames.isShow" class="alert">
    <div class="container" :style="setWidth">
        <h4 v-if="parames.showTitle" class="title">{{ parames.title }}</h4>
        <div v-if="parames.content" class="alert-content" :style="parames.contentStyle">
            <slot name="content">
                <p>{{ parames.content }}</p>
            </slot>
        </div>
        <div v-if="parames.cancel && parames.makesure" class="bottom">
            <a @click="cancelFn" class="cancel" href="javascript:">{{ parames.cancel }}</a>
            <a @click="makesureFn" class="makesure" href="javascript:">{{ parames.makesure }}</a>
        </div>
        <i @click="closeFn" v-if="parames.showTitle">×</i>
    </div>
</div>
</template>

<script>
export default {
    name: 'alert',
    props: {
        parames: {
            type: Object,
            default: function () {
                return {
                    isShow: false,
                    width: 300,
                    title: '',
                    showTitle: true,
                    time: ''
                }
            }
        }
    },
    data () {
        return {
            t: null
        }
    },
    watch: {
        'parames.isShow': function (val, oldval) {
            clearTimeout(this.t)
            if (this.parames.time) {
                this.t = setTimeout(() => {
                    this.closeFn()
                    clearTimeout(this.t)
                }, parseInt(this.parames.time))
            }
        }
    },
    computed: {
        setWidth: function () {
            return 'width: ' + this.parames.width + 'px;'
        }
    },
    methods: {
        cancelFn () {
            this.$emit('cancel')
        },
        makesureFn () {
            this.$emit('makesure')
        },
        closeFn () {
            this.$emit('close')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.alert {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.7);
    text-align: center;
    z-index: 99999;
    &:before {
        height: 100%;
        display: inline-block;
        content: '';
        vertical-align: middle;
    }
    .container {
        width: 300px;
        padding-bottom: 10px;
        position: relative;
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        background-color: #fff;
        p {
            margin: 6px 0;
        }
        .alert-content {
            padding: 10px;
        }
        .bottom {
            display: flex;
            justify-content: space-around;
            align-items: center;
            line-height: 36px;
            padding: 4px 20px;
            margin-top: 10px;
            a {
                padding: 0 30px;
                text-align: center;
                color: #fff;
                border-radius: 4px;
            }
            a:hover {
                text-decoration: underline;
            }
            .cancel {
                background-color: #ff6f0a;
            }
            .makesure {
                background-color: #5cb85c;
            }
        }
    }
}
h4 {
    height: 40px;
    line-height: 40px;
    margin: 0;
    padding-left: 30px;
    background-color: #f4f4f4;
    border-bottom: 1px solid #ddd;
    font-size: 16px;
}
i {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    text-align: center;
    color: #333;
    cursor: pointer;
    border-radius: 50%;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
}
</style>



