<template>
<div class="user-capital">
    <div class="capital-detail">
        <h4>资产总额：<span v-if="capital.availableAmount">{{ (capital.availableAmount + capital.frozenAmount + capital.dueInAmount) }}</span> 元</h4>
        <div class="wrap">
            <div>
                <p>投资次数：<span>{{ capital.investCount }}</span></p>
                <p>借款次数：<span>{{ capital.loanCount }}</span></p>
            </div>
            <div>
                <p>可用金额：<span>{{ capital.availableAmount }} 元</span></p>
                <p>冻结金额：<span>{{ capital.frozenAmount }} 元</span></p>
            </div>
            <div>
                <p>待收金额：<span>{{ capital.dueInAmount }} 元</span></p>
                <p>待还金额：<span>{{ capital.dueOutAmount }} 元</span></p>
            </div>
        </div>
    </div>
    <div class="search-bar">
        <span>资金类型：</span>
        <selection :arr="capitalType" @selected="capitalTypeCallback"></selection>&nbsp;&nbsp;
        <span>日期范围：</span>
        <my-datepicker @changed="dateChange" ref="dateStart" time="start"></my-datepicker> - 
        <my-datepicker @changed="dateChange" ref="dateEnd" time="end"></my-datepicker>&nbsp;&nbsp;
        <span>状态：</span>
        <selection :arr="statusList" @selected="statusCallback"></selection>
        <span @click="searchFn" class="btn">查询</span>
    </div>
    <table>
        <thead>
            <tr>
                <th>订单号</th>
                <th>金额（元）</th>
                <th>类型</th>
                <th>状态</th>
                <th>时间</th>
                <th>备注</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ item.orderId }}</td>
                <td>{{ item.amount }}</td>
                <td>{{ item.strType }}</td>
                <td>{{ item.strStatus }}</td>
                <td>{{ item.strTimeRecorded }}</td>
                <td>{{ item.description }}</td>
            </tr>
            <tr v-if="!list.length">
                <td colspan="6">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
</div>
</template>

<script>
import myDatepicker from '../../submodel/my-datepicker.vue'
import alert from '../../submodel/alert.vue'
import pagination from '../../submodel/pagination.vue'
import selection from '../../submodel/selection.vue'
export default {
    name: 'user-capital',
    data () {
        return {
            capitalType: [
                {
                    lable: '全部资金类型',
                    val: ''
                },
                {
                    lable: '投标',
                    val: 'INVEST'
                },
                {
                    lable: '取现',
                    val: 'WITHDRAW'
                },
                {
                    lable: '充值',
                    val: 'DEPOSIT'
                },
                {
                    lable: '放款',
                    val: 'LOAN'
                },
                {
                    lable: '贷款还款',
                    val: 'LOAN_REPAY'
                },
                {
                    lable: '垫付还款',
                    val: 'DISBURSE'
                },
                {
                    lable: '投资还款',
                    val: 'INVEST_REPAY'
                },
                {
                    lable: '流标返款',
                    val: 'FAILED_LOAN_REPAY'
                },
                {
                    lable: '提现手续费',
                    val: 'FEE_WITHDRAW'
                },
                {
                    lable: '投资管理费',
                    val: 'FEE_INVEST_INTEREST'
                },
                {
                    lable: '借款服务费',
                    val: 'FEE_LOAN_SERVICE'
                },
                {
                    lable: '借款管理费',
                    val: 'FEE_LOAN_MANAGE'
                },
                {
                    lable: '还款管理费',
                    val: 'FEE_LOAN_INTEREST'
                },
                {
                    lable: '风险管理费',
                    val: 'FEE_LOAN_RISK'
                },
                {
                    lable: '担保费',
                    val: 'FEE_LOAN_GUARANTEE'
                },
                {
                    lable: '充值手续费',
                    val: 'FEE_DEPOSIT'
                },
                {
                    lable: '借款人还款',
                    val: 'BORROWER_REPAY'
                }
            ],
            capitalIndex: 0,
            statusList: [
                {
                    lable: '全部',
                    val: ''
                },
                {
                    lable: '初始',
                    val: 'INITIALIZED'
                },
                {
                    lable: '处理中',
                    val: 'PROCESSING'
                },
                {
                    lable: '审核中',
                    val: 'AUDITING'
                },
                {
                    lable: '成功',
                    val: 'SUCCESSFUL'
                },
                {
                    lable: '失败',
                    val: 'FAILED'
                },
                {
                    lable: '拒绝',
                    val: 'REJECTED'
                },
                {
                    lable: '取消',
                    val: 'CANCELED'
                },
                {
                    lable: '支付结果待查',
                    val: 'PAY_PENDING'
                }
            ],
            statusIndex: 0,
            capital: {},
            list: [],
            parame: {
                start: '',
                end: '',
                now: 1,
                all: 1
            }
        }
    },
    components: {
        myDatepicker,
        alert,
        pagination,
        selection
    },
    created () {
        this.$nextTick(() => {
            this.getCapital()
            this.getList()
        })
    },
    methods: {
        getCapital () {
            let This = this
            this.$ajax('/back/user/searchUserFundDetail', {
                'id': this.$route.query.id
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.capital = data.data.userFundDetail
                }
            }, function (err) {
                console.log(err)
            })
        },
        getList () {
            let This = this
            this.$ajax('/back/fundRecord/searchFundRecordByPage', {
                'page': {
                    pageNo: this.parame.now,
                    pageSize: 10,
                    strStartTime: this.parame.start,
                    strEndTime: this.parame.end,
                    params: {
                        status: this.capitalType[this.capitalIndex].val,
                        type: this.statusList[this.statusIndex].val,
                        userId: this.$route.query.id
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results
                    This.parame.all = data.data.page.totalPage
                }
            }, function (err) {
                console.log(err)
            })
        },
        searchFn () {
            this.getList()
        },
        dateChange (val, type) {
            this.parame[type] = val.time
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            console.log('cencel')
        },
        makesureAlert () {
            console.log('ok')
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getList()
        },
        capitalTypeCallback (index) {
            this.capitalIndex = index
        },
        statusCallback (index) {
            this.statusIndex = index
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.capital-detail {
    padding: 10px;
    background-color: #e4e4e4;
    h4 {
        margin: 0;
        height: 40px;
        line-height: 40px;
        text-align: center;
        span {
            color: red;
        }
    }
    .wrap {
        display: flex;
        justify-content: space-around;
        p {
            line-height: 40px;
            color: #666;
            font-size: 14px;
            margin: 0;
            span {
                color: #333;
            }
        }
    }
}
.search-bar {
    padding: 10px 0;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.user-capital {
    padding-top: 10px;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
</style>



