<template>
<div class="short">
    <h4>短信群发</h4>
    <div class="btn-bar">
        <span class="setColor">时间区间：</span>
        <my-datepicker @changed="dateChange" ref="dateStart" time="start"></my-datepicker> - 
        <my-datepicker @changed="dateChange" ref="dateEnd" time="end"></my-datepicker>
        <span @click="getData('/back/user/getUserMobiles')" class="btn">注册查询</span>
        <span @click="getData('/back/user/queryBirthDateUsers')" class="btn">生日查询</span>
        <span @click="getData('/back/user/queryInvestUsers')" class="btn">投资用户查询</span>
    </div>
    <p class="pho">{{ mobiles.join(',') }}</p>
    <p class="num">总共选择了：<span>{{ mobiles.length }}</span>个电话号码</p>
    <textarea rows="4" cols="60" v-model="message"></textarea>
    <div>
        <span>定时发送：</span>
        <my-datepicker @changed="dateChange" ref="dateDetail" time="detail" type="min"></my-datepicker>
        <span @click="sendMessage" class="btn">发送</span>
    </div>
    <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames"></alert>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import myDatepicker from '../../submodel/my-datepicker.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'short',
    data () {
        return {
            parame: {
                start: '',
                end: '',
                detail: ''
            },
            mobiles: [],
            message: '',
            alertParames: {
                isShow: false,
                width: 300,
                title: '',
                content: '111111111',
                cancel: 'cancel',
                makesure: 'ok',
                showTitle: true,
                contentStyle: 'text-align: center;',
                time: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData (str) {
            if (!this.parame.start || !this.parame.end) {
                this.msg.content = '时间为必选'
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            let This = this
            This.mobiles = []
            This.msg.content = '查询中...'
            This.msg.isShow = true
            this.$ajax(str, {
                startTime: this.parame.start,
                endTime: this.parame.end
            }, function (data) {
                This.msg.isShow = false
                if (data.data.message.code === 1000) {
                    This.mobiles = data.data.mobiles || []
                } else {
                    setTimeout(() => {
                        This.msg.content = data.data.message.message
                        This.msg.time = '1500'
                        This.msg.isShow = true
                    }, 0)
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        sendMessage () {
            let This = this
            if (!this.mobiles.length || !this.message) {
                if (!this.mobiles.length) {
                    this.msg.content = '手机号码为空'
                } else if (!this.message) {
                    this.msg.content = '信息内容不能为空'
                }
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            This.msg.content = '发送中...'
            This.msg.isShow = true
            this.$ajax('/back/user/sensMessageToUser', {
                sendTime: this.parame.detail,
                message: this.message,
                mobiles: this.mobiles.join(',')
            }, function (data) {
                This.msg.isShow = false
                setTimeout(() => {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }, 0)
                if (data.data.message.code === 1000) {
                    This.message = ''
                    This.mobiles = []
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        dateChange (val, type) {
            this.parame[type] = val.time
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            console.log('cencel')
        },
        makesureAlert () {
            console.log('ok')
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        myDatepicker,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h4 {
    color: #333;
    border-bottom: 1px solid #38afff;
}
.setColor {
    color: #333;
    font-size: 14px;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.pho {
    border: 1px solid #ddd;
    padding: 20px;
    word-break:break-all;
    word-wrap:break-word;
}
.num span {
    color: red;
}
input {
    border: 1px solid #e4e4e4;
}
textarea {
    padding: 10px;
    outline: none;
}
</style>



