<template>
<div class="product">
    <div class="head">
        <p>产品列表</p>
        <a @click="edit(1)" href="javascript:">新建产品</a>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>产品名称</th>
                    <th>借款期限（月）</th>
                    <th>借款年利率（%）</th>
                    <th>描述</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in dataList">
                    <td><a @click="edit(2, index)" href="javascript:">{{ item.name }}</a></td>
                    <td>{{ item.months }}</td>
                    <td>{{ item.rate }}</td>
                    <td>{{ item.description }}</td>
                    <td><a @click="edit(2, index)" href="javascript:">编辑</a><a @click="disable(item)" href="javascript:">禁用</a></td>
                </tr>
                <tr v-if="!dataList.length">
                    <td colspan="5">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <div v-show="newFrameShow" class="product-detail">
        <div class="new-produt-wrap">
            <i @click="newFrameShow = false">X</i>
            <h4>{{ title }}</h4>
            <div class="row">
                <p>产品名称：</p>
                <input type="text" v-model="newData.name" />
            </div>
            <div class="row">
                <p>还款方式：</p>
                <selection :indexDefault="payIndex" :arr="paymentMethod" @selected="paymentFn"></selection>
            </div>
            <div class="row">
                <p>借款期限（月）：</p>
                <input type="text" v-model="newData.months" />
            </div>
            <div class="row">
                <p>借款利率：</p>
                <input type="text" v-model="newData.rate" />
            </div>
            <div class="row">
                <p>投资人群：</p>
                <selection :index-default="cusIndex" :arr="customerType" @selected="customerFn"></selection>
            </div>
            <div class="row">
                <p>最小投资金额（元）：</p>
                <input type="text" v-model="newData.minInvestAmount" />
            </div>
            <div class="row">
                <p>最大投资金额（元）：</p>
                <input type="text" v-model="newData.maxInvestAmount" />
            </div>
            <div class="row">
                <p>产品描述：</p>
                <textarea rows="3"  v-model="newData.description"></textarea>
            </div>
            <p class="btn">
                <input @click="newProduct" type="button" value="保存"/>
            </p>
        </div>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
    <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames"></alert>
</div>
</template>

<script>
import selection from '../submodel/selection.vue'
import alert from '../submodel/alert.vue'
export default {
    name: 'product',
    data () {
        return {
            dataList: [],
            paymentMethod: [
                {
                    lable: '按月付息到期还本',
                    val: 'MonthlyInterest'
                },
                {
                    lable: '按月等额本息',
                    val: 'EqualInstallment'
                },
                {
                    lable: '按月等额本金',
                    val: 'EqualPrincipal'
                },
                {
                    lable: '一次性还本付息',
                    val: 'BulletRepayment'
                },
                {
                    lable: '月平息',
                    val: 'EqualInterest'
                },
                {
                    lable: '按年付息到期还本',
                    val: 'YearlyInterest'
                }
            ],
            payIndex: 0,
            customerType: [
                {
                    lable: '全部用户',
                    val: 1
                },
                {
                    lable: '新用户',
                    val: 0
                }
            ],
            cusIndex: 0,
            title: '新建产品',
            newFrameShow: false,
            newData: {
                name: '',
                repayMethod: '',
                months: '',
                rate: '',
                groupId: '',
                minInvestAmount: '',
                maxInvestAmount: '',
                description: '',
                id: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            alertParames: {
                isShow: false,
                width: 300,
                title: '',
                content: '',
                cancel: '取消',
                makesure: '确定',
                contentStyle: 'text-align: center;',
                time: ''
            },
            disableData: {}
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/product/queryAllProducts', {}, function (data) {
                This.dataList = data.data.products
            }, function (err) {
                console.log(err)
            })
        },
        edit (flg, index) {
            if (flg === 1) {
                this.title = '新建产品'
                this.payIndex = 0
                this.cusIndex = 0
            } else if (flg === 2) {
                this.title = '编辑/查看产品'
                this.payIndex = this.getIndex(this.dataList[index].repayMethod, this.paymentMethod)
                this.cusIndex = this.getIndex(this.dataList[index].groupId, this.customerType)
            }
            this.newFrameShow = true
            this.newData.name = this.dataList[index] ? this.dataList[index].name : ''
            this.newData.months = this.dataList[index] ? this.dataList[index].months : ''
            this.newData.rate = this.dataList[index] ? this.dataList[index].rate : ''
            this.newData.minInvestAmount = this.dataList[index] ? this.dataList[index].minInvestAmount : ''
            this.newData.maxInvestAmount = this.dataList[index] ? this.dataList[index].maxInvestAmount : ''
            this.newData.description = this.dataList[index] ? this.dataList[index].description : ''
            this.newData.id = this.dataList[index] ? this.dataList[index].id : ''
        },
        disable (obj) {
            this.alertParames.isShow = true
            this.alertParames.content = '你确定要禁用该用户吗？'
            this.disableData = obj
        },
        newProduct () {
            let This = this
            if (!this.newData.name || !this.newData.months || !this.newData.rate || !this.newData.minInvestAmount || !this.newData.maxInvestAmount) {
                if (!this.newData.name) {
                    This.msg.content = '请输入产品名称'
                } else if (!this.newData.months) {
                    This.msg.content = '请输入借款期限'
                } else if (!this.newData.rate) {
                    This.msg.content = '请输入借款利率'
                } else if (!this.newData.minInvestAmount) {
                    This.msg.content = '请输入最小投资金额'
                } else if (!this.newData.maxInvestAmount) {
                    This.msg.content = '请输入最大投资金额'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            let url = this.newData.id ? '/back/product/modifyProduct' : '/back/product/addProduct'
            this.$ajax(url, {
                product: {
                    name: this.newData.name,
                    repayMethod: this.paymentMethod[this.payIndex].val,
                    months: this.newData.months,
                    rate: this.newData.rate,
                    groupId: this.customerType[this.cusIndex].val,
                    minInvestAmount: this.newData.minInvestAmount,
                    maxInvestAmount: this.newData.maxInvestAmount,
                    description: this.newData.description,
                    id: this.newData.id
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    setTimeout(() => {
                        This.newFrameShow = false
                        This.getData()
                    }, 1500)
                }
            }, function (err) {
                console.log(err)
            })
        },
        getIndex (val, arr) {
            let i = 0
            arr.forEach(function (item, index) {
                if (item.val === val) {
                    i = index
                }
            })
            return i
        },
        paymentFn (index) {
            this.payIndex = index
        },
        customerFn (index) {
            this.cusIndex = index
        },
        closeMsg () {
            this.msg.isShow = false
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            this.alertParames.isShow = false
        },
        makesureAlert () {
            this.alertParames.isShow = false
            let This = this
            this.$ajax('/back/product/modifyProduct', {
                product: {
                    status: 1,
                    id: This.disableData.id
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                This.getData()
            }, function (err) {
                console.log(err)
            })
        }
    },
    components: {
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.product {
    position: relative;
}
.head {
    display: flex;
    justify-content: space-between;
    p {
        margin: 0;
    }
    p, a {
        display: block;
        padding: 6px 12px;
        font-size: 18px;
    }
    a {
        color: #38afff;
        &:hover {
            text-decoration: underline;
        }
    }
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.product-detail {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    text-align: center;
    .new-produt-wrap {
        display: inline-block;
        position: relative;
    }
}
h4 {
    font-size: 16px;
    border-bottom: 1px solid #38afff;
}
.row {
    width: 580px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    p {
        text-align: right;
        width: 200px;
    }
}
.btn {
    text-align: center;
    input {
        width: 120px;
        background-color: #289cff;
        height: 40px;
        border-radius: 4px;
        color: #fff;
        cursor: pointer;
        border: 0;
        &:hover {
            text-decoration: underline;
        }
    }
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
i {
    position: absolute;
    font-style: normal;
    top: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-weight: 400;
    font-size: 18px;
    color: red;
    cursor: pointer;
    border-radius: 50%;
}
</style>



