<template>
<ul>
    <li v-for="(item, index) in list">
        <h4 @click="choose(item.id, item.orgName)">{{ item.orgName }}</h4>
        <tree @choosed="chooseFn" :list="item.children"></tree>
    </li>
</ul>
</template>

<script>
export default {
    name: 'tree',
    props: {
        list: {
            type: Array,
            default: function () {
                return []
            }
        }
    },
    data () {
        return {
        }
    },
    methods: {
        choose (id, name) {
            this.$emit('choosed', id, name)
        },
        chooseFn (id, name) {
            this.choose(id, name)
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
ul li {
    position: relative;
    > ul {
        position: absolute;
        top: 0;
        left: 100%;
    }
}
h4 {
    padding: 0 30px;
    margin: 0;
    white-space:nowrap;
    height: 36px;
    line-height: 36px;
    cursor: pointer;
    background-color: #438eb9;
    color: #fff;
}
li ul {
    display: none;
}
li:hover > ul {
    display: block;
}
li:hover > h4 {
    background-color: #75bcec;
}
</style>



