<template>
<div class="modify-password">
    <p class="title">修改登录密码</p>
    <div class="container">
        <p class="old-passWord">
            <span class="msg">旧密码：</span>
            <input type="password" v-model="oldPassword"/>
        </p>
        <p class="new-pssWord">
            <span class="msg">新密码：</span>
            <input placeholder="6~16位字符" v-model="newPassword" type="password" />
        </p>
        <p class="new-pssWord">
            <span class="msg">确认密码：</span>
            <input placeholder="与新密码保持一致" v-model="makesure" type="password" />
        </p>
        <p class="btn">
            <input @click="modifyPassword" type="button" value="确定"/>
        </p>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'modify-password',
    data () {
        return {
            oldPassword: '',
            newPassword: '',
            makesure: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        modifyPassword () {
            let This = this
            if (this.newPassword.length > 16 || this.newPassword.length < 6 || this.newPassword !== this.makesure) {
                if (this.newPassword.length > 16) {
                    This.msg.content = '新密码不能大于16位'
                } else if (this.newPassword.length < 6) {
                    This.msg.content = '新密码不能小于6位'
                } else if (this.newPassword !== this.makesure) {
                    This.msg.content = '2次输入密码不一致'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/emp/empModifyInfo', {
                emp: {
                    passphrase: this.oldPassword,
                    newPassWord: this.newPassword
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    this.oldPassword = ''
                    this.newPassword = ''
                    this.makesure = ''
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.modify-password {
    width: 500px;
    margin: 60px auto 0;
    .title {
        line-height: 40px;
        font-weight: bold;
        color: #363636;
    }
    span, input {
        display: inline-block;
        vertical-align: center;
        padding: 6px;
    }
    input {
        border: 1px solid #ccc;
    }
    .msg {
        width: 70px;
        font-size: 14px;
    }
    .btn {
        padding-left: 120px;
        input {
            width: 120px;
            background-color: #289cff;
            height: 30px;
            border-radius: 4px;
            color: #fff;
            cursor: pointer;
            border: 0;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}
</style>



