<template>
<div v-if="isShow" class="repayment-popup">
    <div class="tip">
        <ul>
            <li>
                <span>应还本金:</span>
                <span>{{ detail.amountPrincipal }}元</span>
            </li>
            <li>
                <span>应还利息:</span>
                <span>{{ detail.amountInterest }}元</span>
            </li>
            <li>
                <span>金额总计:</span>
                <span>{{ total }}元</span>
            </li>
        </ul>
        <div class="btn">
            <a @click="payFn" :class="[repayType]">{{ payfor }}</a>
        </div>
        <i @click="closeFn">×</i>
    </div>
</div>
</template>

<script>
export default {
    name: 'repayment-popup',
    props: {
        detail: {
            type: Object,
            default: {}
        },
        isShow: {
            default: false
        },
        repayType: {
            type: String,
            required: true
        }
    },
    computed: {
        payfor: function () {
            return (this.repayType === 'repayment') ? '确认还款' : '确认垫付'
        },
        total: function () {
            return parseFloat(this.detail.amountPrincipal) + parseFloat(this.detail.amountInterest)
        }
    },
    data () {
        return {
        }
    },
    methods: {
        payFn () {
            this.$emit('pay')
        },
        closeFn () {
            this.$emit('closePopup')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.repayment-popup {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    text-align: center;
    background-color: rgba(0,0,0,0.5);
    &:before {
        height: 100%;
        display: inline-block;
        content: '';
        vertical-align: middle;
    }
    .tip {
        padding: 20px;
        display: inline-block;
        vertical-align: middle;
        background-color: #fff;
        position: relative;
        li {
            line-height: 30px;
            font-size: 14px;
        }
        span {
            color: #666;
        }
        .btn {
            text-align: center;
            padding-top: 10px;
        }
        a {
            display: inline-block;
            padding: 0 20px;
            height: 30px;
            line-height: 30px;
            border-radius: 4px;
            background-color: #38afff;
            cursor: pointer;
            color: #fff;
        }
        .pay {
            background-color: #ff302b;
        }
        i {
            position: absolute;
            font-style: normal;
            top: 8px;
            right: 8px;
            width: 20px;
            height: 20px;
            line-height: 20px;
            text-align: center;
            font-weight: 700;
            font-size: 16px;
            color: #fff;
            cursor: pointer;
            border-radius: 50%;
            background-color: #ff5825;
        }
    }
}
</style>



