<template>
<div class="zjbdmx">
    <div class="head">
        订单号：<input v-model="parame.order_no" type="text" placeholder="选填">&nbsp;&nbsp;
        客户编号：<input v-model="parame.mobile" type="text" placeholder="客户手机号或平台编号（必填）" size="26">&nbsp;&nbsp;
        平台子账户类型：<selection class="inline-block" :index-default="accountTypeIndex" :arr="accountTypeList" @selected="accountTypeFn"></selection>
        交易名称：<selection class="inline-block" :index-default="transIndex" :arr="trans" @selected="transFn"></selection>
        页码：<input v-model="parame.now" type="text" placeholder="必填" size="2">&nbsp;&nbsp;
        <v-search class="search-bar" @searchFn="searchFn" @changeVal="searchValChange"></v-search>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>平台编号</th>
                    <th>客户编号</th>
                    <th>交易类型</th>
                    <th>交易金额</th>
                    <th>交易日期</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td>{{ item.plat_no }}</td>
                    <td>{{ item.platcust }}</td>
                    <td>{{ item.amt_type }}</td>
                    <td>{{ item.amt }}</td>
                    <td>{{ item.trans_date + ' ' + item.trans_time }}</td>
                </tr>
                <tr v-if="!list.length && parame.queryEnd">
                    <td colspan="5">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'zjbdmx',
    data () {
        return {
            list: [],
            accountTypeList: [
                {
                    lable: '请选择账户类型',
                    val: ''
                },
                {
                    lable: '平台自有子账户 ',
                    val: '1'
                },
                {
                    lable: '手续费子账户',
                    val: '3'
                },
                {
                    lable: '体验金子账户',
                    val: '4'
                },
                {
                    lable: '低佣金子账户',
                    val: '5'
                },
                {
                    lable: '加息活动金子账户',
                    val: '6'
                },
                {
                    lable: '保证金子账户',
                    val: '7'
                },
                {
                    lable: '奖励金子账户',
                    val: '9'
                },
                {
                    lable: '现金垫付子账户',
                    val: '10'
                },
                {
                    lable: '在途垫付子账户',
                    val: '11'
                }
            ],
            accountTypeIndex: 0,
            trans: [
                {
                    lable: '请选择交易名称',
                    val: ''
                },
                {
                    lable: '投标',
                    val: '投标'
                },
                {
                    lable: '提现',
                    val: '提现'
                },
                {
                    lable: '标的出账',
                    val: '标的出账'
                },
                {
                    lable: '债权转让',
                    val: '债权转让'
                },
                {
                    lable: '标的成立',
                    val: '标的成立'
                },
                {
                    lable: '标的废弃',
                    val: '标的废弃'
                },
                {
                    lable: '标的出账',
                    val: '标的出账'
                },
                {
                    lable: '标的还款',
                    val: '标的还款'
                },
                {
                    lable: '借款人还款',
                    val: '借款人还款'
                },
                {
                    lable: '充值',
                    val: '充值'
                },
                {
                    lable: '投融资转换',
                    val: '投融资转换'
                },
                {
                    lable: '转账',
                    val: '转账'
                }
            ],
            transIndex: 0,
            parame: {
                now: 1,
                startTime: '',
                endTime: '',
                order_no: '',
                mobile: '',
                queryEnd: false
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.parame.mobile || !this.parame.now) {
                if (!this.parame.mobile) {
                    This.msg.content = '请输入客户手机号或平台编号'
                } else if (!this.parame.now) {
                    This.msg.content = '请输入页码'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/seachPlatcustFundDetail', {
                'order_no': this.parame.order_no,
                'platcust': this.parame.mobile,
                'start_date': this.parame.startTime,
                'end_date': this.parame.endTime,
                'trans_name': this.trans[this.transIndex].val,
                'pagenum': this.parame.now,
                'acct_type': this.accountTypeList[this.accountTypeIndex].val
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.list || []
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
                This.parame.queryEnd = true
            }, function (err) {
                console.log(err)
                This.parame.queryEnd = true
            })
        },
        searchFn () {
            this.getData()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        accountTypeFn (index) {
            this.accountTypeIndex = index
        },
        transFn (index) {
            this.transIndex = index
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        'v-search': search,
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.head {
    padding: 10px 0;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.search-bar {
    display: inline-block;
    margin-left: 10px;
}
</style>



