<template>
<div class="bdzhye">
    <p>
        标的ID：<input v-model="prodId" type="text" placeholder="必填" size="50"/>&nbsp;&nbsp;
        类型：<selection class="inline-block" :index-default="capitalTypeIndex" :arr="capitalTypeList" @selected="capitalTypeFn"></selection>
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>标的ID</th>
                    <th>余额</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{{ obj.prod_id }}</td>
                    <td>{{ obj.balance }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'bdzhye',
    data () {
        return {
            obj: {},
            prodId: '',
            capitalTypeList: [
                {
                    lable: '请选择资金类型',
                    val: ''
                },
                {
                    lable: '现金',
                    val: '01'
                },
                {
                    lable: '在途',
                    val: '02'
                }
            ],
            capitalTypeIndex: 0,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.prodId) {
                This.msg.content = '请输入标的ID'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchLoanAccountBalance', {
                'prod_id': this.prodId,
                'type': this.capitalTypeList[this.capitalTypeIndex].val
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.loanAccount || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        capitalTypeFn (index) {
            this.capitalTypeIndex = index
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



