<template>
<div class="login" @keydown.enter="loginFn">
    <div class="login-frame">
        <h3>中阳财富员工登陆</h3>
        <div class="container">
            <p>
                <span class="msg">用户名</span>
                <input type="text"  v-model="username"/>
            </p>
            <p>
                <span class="msg">密码</span>
                <input class="password" type="password"  v-model="password"/>
            </p>
            <p>
                <span class="msg">验证码</span>
                <input type="text" v-model="imgCode"/>
                <img :src="[devUrl]" height="30" width="94" alt="验证码" />
                <span @click="refreshFn" class="refresh">刷新</span>
            </p>
            <p class="btn">
                <input @click="loginFn" type="button" value="登录"/>
            </p>
        </div>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../submodel/alert.vue'
export default {
    name: 'login',
    data () {
        return {
            devUrl: '',
            username: '',
            password: '',
            imgCode: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getImg()
            this.username = ''
            this.password = ''
            this.imgCode = ''
        })
    },
    methods: {
        refreshFn () {
            this.getImg()
        },
        loginFn () {
            let This = this
            if (this.username === '' || this.password === '' || this.imgCode === '') {
                This.msg.content = '请填写完整表单'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/emp/empLogin', {
                empLoginParams: {
                    loginName: this.username,
                    passphrase: this.password,
                    code: this.imgCode
                }
            }, function (data) {
                This.$store.commit('updateUserInfo', data.data)
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.$router.push({path: 'user-list'})
                }
            }, function (err) {
                console.log(err)
            })
        },
        getImg () {
            let This = this
            this.$get('/back/util/imgValalidate' + '?' + Math.random(), function (data) {
                This.devUrl = 'data:image/png;base64,' + data.data
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.refresh, input[type='button']{
    -khtml-user-select: none;
    user-select: none;
}
.login {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    text-align: center;
    background-color: #e0e0e0;
    &:before {
        height: 100%;
        display: inline-block;
        content: '';
        vertical-align: middle;
    }
    .login-frame {
        width: 500px;
        padding-bottom: 30px;
        display: inline-block;
        vertical-align: middle;
        background-color: #fff;
        h3 {
            height: 56px;
            background-color: #75bcec;
            color: #fff;
            font-size: 24px;
            text-align: center;
            line-height: 56px;
            margin: 0;
        }
        p {
            line-height: 30px;
            text-align: left;
            padding-left: 60px;
            span, img, input {
                display: inline-block;
                vertical-align: middle;
            }
            .msg {
                width: 60px;
                text-align: right;
                color: #666;
            }
            input {
                width: 160px;
                height: 20px;
                padding: 6px 5px;
                border: 1px solid #ccc;
            }
            .refresh {
                width: 44px;
                height: 34px;
                border-radius: 4px;
                background-color: #75bcec;
                color: #fff;
                font-size: 14px;
                text-align: center;
                cursor: pointer;
                line-height: 34px;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        .btn {
            padding: 0;
            text-align: center;
            input {
                width: 284px;
                height: 42px;
                border-radius: 4px;
                background-color: #ff6f0a;
                color: #fff;
                font-size: 18px;
                cursor: pointer;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}
.faild {
    border-color: red  !important;
}
.success {
    border-color: #1CA72D  !important;
}
</style>



