<template>
<div class="column">
    <div class="head">
        <span @click="addColumn" class="btn">新建栏目</span>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>栏目名称</th>
                    <th>时间</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in columuList">
                    <td>{{ item.name }}</td>
                    <td>{{ item.time }}</td>
                    <td>
                        <a @click="edit(item)" href='javascript:void(0);'>编辑</a>
                        <a @click="del(item)" href='javascript:void(0);'>删除</a>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames">
        <template slot="content">
            <div class="row">
                <p>名称：</p>
                <input v-model="columnName" type="text" />
            </div>
        </template>
    </alert>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'column',
    data () {
        return {
            columuList: [],
            alertParames: {
                isShow: false,
                width: 400,
                showTitle: true,
                title: '新建栏目',
                content: '1',
                cancel: '取消',
                makesure: '确定'
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            columnName: '',
            editData: {}
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/cms/queryCmsColumnByParams', {}, function (data) {
                if (data.data.message.code === 1000) {
                    This.columuList = data.data.columnvos
                }
            }, function (err) {
                console.log(err)
            })
        },
        addColumn () {
            this.editData = {}
            this.columnName = ''
            this.alertParames.isShow = true
        },
        del (obj) {
            let This = this
            this.$ajax('/back/cms/deleteCmsColumnByParams', {
                id: obj.id
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                This.alertParames.isShow = false
                if (data.data.message.code === 1000) {
                    setTimeout(() => {
                        This.getData()
                    }, 1500)
                }
            }, function (err) {
                console.log(err)
            })
        },
        edit (obj) {
            this.alertParames.isShow = true
            this.editData = obj
            this.columnName = obj.name
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
           this.alertParames.isShow = false
        },
        makesureAlert () {
            let This = this
            let url = ''
            if (this.columnName === '') {
                return
            }
            if (this.editData.id) {
                url = '/back/cms/modifyCmsColumnByParams'
            } else {
                url = '/back/cms/addCmsColumn'
            }
            this.$ajax(url, {
                name: this.columnName,
                id: this.editData.id ? this.editData.id : ''
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                This.alertParames.isShow = false
                if (data.data.message.code === 1000) {
                    setTimeout(() => {
                        This.getData()
                    }, 1500)
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.head {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
}
.btn {
    display: inline-block;
    padding: 4px 8px;
    background-color: #38afff;
    color: #fff;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.row {
    width: 400px;
    margin: 10px auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    p {
        text-align: right;
        width: 100px;
    }
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



