<template>
<div class="re-referee">
    <div>
        <h4>手机号变更</h4>
        <span>原手机号：</span>
        <input v-model="oldMobile" type="text" value="" placeholder="原手机号"/> -- 
        <span>新手机号：</span>
        <input v-model="newMobile" type="text" value="" placeholder="新手机号"/>
        <span @click="modifyFn" class="btn">确定</span>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 're-referee',
    data () {
        return {
            oldMobile: '',
            newMobile: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    components: {
        alert
    },
    methods: {
        modifyFn () {
            let This = this
            if (!this.checkMobile(this.oldMobile) || !this.checkMobile(this.newMobile)) {
                if (!this.checkMobile(this.oldMobile)) {
                    This.msg.content = '原手机号格式不正确'
                } else if (!this.checkMobile(this.newMobile)) {
                    This.msg.content = '新手机号格式不正确'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/bmAccount/modifyMobile', {
                'old_mobile': this.oldMobile,
                'new_mobile': this.newMobile
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.oldMobile = ''
                    This.newMobile = ''
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
input {
    border: 1px solid #ddd;
    padding: 6px 10px;
}
.marginTop {
    margin-top: 20px;
    border-top: 1px solid #ddd;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
</style>



