<template>
<div class="invitation">
    <h4>邀请统计</h4>
    <div class="head">
        <tree-selection @treeChange="treeCallback" ref="treeselection" :list="treeDate" :tree-title="treeTitle"></tree-selection>&nbsp;&nbsp;
        <v-search class="inline" @searchFn="searchFn" @changeVal="searchValChange"></v-search>
        <span class="btn export">导出</span>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>员工姓名</th>
                    <th>手机号码</th>
                    <th>机构名称</th>
                    <th>总投资金额</th>
                    <th>年化总投资金额</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td @click="toDetail(item)"><a href="javascript:">{{ item.staffName }}</a></td>
                    <td>{{ item.mobile }}</td>
                    <td>{{ item.orgName }}</td>
                    <td>{{ item.totalAmount }}</td>
                    <td>{{ item.yearAmount }}</td>
                </tr>
                <tr v-if="!list.length">
                    <td colspan="5">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <invitation-ref v-if="childrenShow" @close="closeChild" :obj="childObj" ></invitation-ref>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import treeSelection from '../../submodel/tree-selection.vue'
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
import invitationRef from '../invitation-ref/invitation-ref.vue'
export default {
    name: 'invitation',
    data () {
        return {
            list: [],
            treeDate: [],
            treeTitle: '机构：',
            organizationId: '',
            parame: {
                now: 1,
                all: 1,
                startTime: '',
                endTime: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            childrenShow: false,
            childObj: {}
        }
    },
    created () {
        this.$nextTick(() => {
            this.getJigou()
        })
    },
    methods: {
        getData () {
            let This = this
            This.msg.content = '查询中...'
            This.msg.isShow = true
            this.$ajax('/back/statistic/queryStatisticByOrg', {
                markPage: {
                    pageNo: this.parame.now,
                    pageSize: 10,
                    strStartTime: this.parame.startTime,
                    strEndTime: this.parame.endTime,
                    params: {
                        organizationId: this.organizationId
                    }
                }
            }, function (data) {
                This.msg.isShow = false
                if (data.data.message.code === 1000) {
                    This.list = data.data.markStaticVo.results || []
                    This.parame.all = data.data.markStaticVo.totalPage
                } else {
                    setTimeout(() => {
                        This.msg.content = data.data.message.message
                        This.msg.time = '1500'
                        This.msg.isShow = true
                    }, 0)
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        getJigou () {
            var This = this
            this.$ajax('/back/org/queryOrganizationByParams', {
                'level': 0
            }, function (data) {
                This.treeDate = data.data.list
            }, function (err) {
                console.log(err)
            })
        },
        treeCallback (val) {
            this.organizationId = val.id
            this.getData()
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getData()
        },
        searchFn () {
            this.getData()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        closeMsg () {
            this.msg.isShow = false
        },
        closeChild () {
            this.childrenShow = false
        },
        toDetail (obj) {
            this.childrenShow = true
            this.childObj = obj
        }
    },
    components: {
        'v-search': search,
        treeSelection,
        pagination,
        alert,
        invitationRef
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h4 {
    margin: 10px 0;
    border-bottom: 1px solid #38afff;
}
.head {
    line-height: 30px;
}
.export {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #ff5825;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.search-bar {
    display: inline-block;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.inline {
    display: inline-block;
}
</style>



