<template>
<div class="flow">
    <v-search @searchFn="searchFn" @changeVal="searchValChange"></v-search>
    <div>
        <table>
            <thead>
                <tr>
                    <th>标的名称</th>
                    <th>关联企业</th>
                    <th>借款人</th>
                    <th>借款人ID</th>
                    <th>标的金额（元）</th>
                    <th>年化利率（%）</th>
                    <th>借款期限</th>
                    <th>进度</th>
                    <th>流标时间</th>
                    <th>原因</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in dataList">
                    <td @click="toDetail(item)"><a href="javascript:">{{ item.title }}</a></td>
                    <td>{{ item.guaranteeRealm }}</td>
                    <td>{{ item.userName }}</td>
                    <td>{{ item.loanUserId }}</td>
                    <td>{{ item.amount }}</td>
                    <td>{{ item.rate }} <span class="add-rate" v-if="item.addRate">+ {{ item.addRate }}</span></td>
                    <td>{{ item.months }}</td>
                    <td>{{ item.planing }}</td>
                    <td>{{ item.timeFailed }}</td>
                    <td>{{ item.reason }}</td>
                </tr>
                <tr v-if="!dataList.length">
                    <td colspan="10">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <alert :parames="msg" @close="closeMsg"></alert>
    <bid-detail class="new-project" @backFn="detailback" v-if="detailFrameShow" :data-obj="detailData"></bid-detail>
</div>
</template>

<script>
import search from '../../submodel/search'
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
import bidDetail from '../bid-detail/bid-detail.vue'
export default {
    name: 'flow',
    data () {
        return {
            parame: {
                now: 1,
                all: 1,
                startTime: '',
                endTime: ''
            },
            dataList: [],
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            detailData: {},
            detailFrameShow: false
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/loan/loanList', {
                page: {
                    pageNo: this.parame.now,
                    pageSize: 10,
                    strStartTime: this.parame.startTime,
                    strEndTime: this.parame.endTime,
                    params: {
                        status: 'FAILED'
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.dataList = data.data.page.results || []
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        searchFn () {
            this.getData()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        },
        toDetail (obj) {
            this.detailFrameShow = true
            this.detailData = obj
        },
        detailback () {
            this.detailFrameShow = false
        }
    },
    components: {
        'v-search': search,
        pagination,
        alert,
        bidDetail
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.flow {
    position: relative;
}
.add-rate {
    color: #ff6f0a;
    font-size: 12px;
}
.new-project {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #fff;
}
</style>



