<template>
<div class="zjyemx">
    <p>
        账号：<input v-model="account" type="text" placeholder="客户手机号或平台编号（01）" size="30"/>&nbsp;&nbsp;
        账户类型：<selection class="inline-block" :index-default="accountTypeIndex" :arr="accountTypeList" @selected="accountTypeFn"></selection>
        资金类型：<selection class="inline-block" :index-default="capitalTypeIndex" :arr="capitalTypeList" @selected="capitalTypeFn"></selection>
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>余额（元）</th>
                    <th>冻结金额（元）</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{{ obj.balance }}</td>
                    <td>{{ obj.frozen_amount }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'zjyemx',
    data () {
        return {
            account: '',
            obj: {},
            accountTypeList: [
                {
                    lable: '请选择账户类型',
                    val: ''
                },
                {
                    lable: '平台自有子账户 ',
                    val: '1'
                },
                {
                    lable: '手续费子账户',
                    val: '3'
                },
                {
                    lable: '体验金子账户',
                    val: '4'
                },
                {
                    lable: '低佣金子账户',
                    val: '5'
                },
                {
                    lable: '加息活动金子账户',
                    val: '6'
                },
                {
                    lable: '保证金子账户',
                    val: '7'
                },
                {
                    lable: '奖励金子账户',
                    val: '9'
                },
                {
                    lable: '现金垫付子账户',
                    val: '10'
                },
                {
                    lable: '在途垫付子账户',
                    val: '11'
                },
                {
                    lable: '投资账户',
                    val: '12'
                },
                {
                    lable: '融资',
                    val: '13'
                }
            ],
            accountTypeIndex: 0,
            capitalTypeList: [
                {
                    lable: '请选择资金类型',
                    val: ''
                },
                {
                    lable: '现金',
                    val: '01'
                },
                {
                    lable: '在途',
                    val: '02'
                }
            ],
            capitalTypeIndex: 0,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.account) {
                This.msg.content = '请输入客户手机号或平台编号'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchAccountBalance', {
                'account': this.account,
                'acct_type': this.accountTypeList[this.accountTypeIndex].val,
                'fund_type': this.capitalTypeList[this.capitalTypeIndex].val
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.accountBalance || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        },
        accountTypeFn (index) {
            this.accountTypeIndex = index
        },
        capitalTypeFn (index) {
            this.capitalTypeIndex = index
        }
    },
    components: {
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.inline-block {
    display: inline-block;
}
</style>



