<template>
<div class="role-permissions">
    <div class="rolelist">
        <p class="title">
            <span>角色列表</span>
            <span @click="alertParames.isShow = true" class="btn">新建角色</span>
        </p>
        <ul class="rolelist-detail">
            <li>
                <h4>角色列表</h4>
                <ul>
                    <li @click="changeIndex(index, item.id)" :class="{active: (index===rolesIndex)}" v-for="(item, index) in roles">{{ item.r_name }}</li>
                </ul>
            </li>
        </ul>
    </div>
    <div class="role-detail">
        <p class="title">
            <span>角色详情</span>
            <span @click="saveAuth" class="btn">保存</span>
        </p>
        <ul class="auth-list">
            <li v-for="(val, key) in auth">
                <h4>{{key}}</h4>
                <ul>
                    <li v-for="(item, index) in val">
                        <input type="checkbox" :id="item.priPindex" :value="item.priPindex" v-model="authList" />
                        <label :for="item.priPindex">{{item.priName}}</label>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
    <div class="memberList">
        <p class="title">
            <span>成员列表</span>
        </p>
        <div class="emps-list">
            <table>
                <thead>
                    <tr>
                        <th>姓名</th>
                        <th>登录名</th>
                        <th>手机号</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in emps">
                        <td>{{item.empName}}</td>
                        <td>{{item.loginName}}</td>
                        <td>{{item.mobile}}</td>
                    </tr>
                    <tr v-if="!emps.length">
                        <td colspan="3">暂无数据</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames">
        <template slot="content">
            <div class="row">
                <p>名称：</p>
                <input v-model="addData.r_name" type="text" />
            </div>
            <div class="row">
                <p>描述：</p>
                <textarea v-model="addData.description" cols="30" rows="3"></textarea>
            </div>
        </template>
    </alert>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'role-permissions',
    data () {
        return {
            roles: [],
            emps: [],
            auth: [],
            rolesIndex: 0,
            authList: [],
            alertParames: {
                isShow: false,
                width: 400,
                showTitle: true,
                title: '新建角色',
                content: '1',
                cancel: '取消',
                makesure: '确定'
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            addData: {
                r_name: '',
                description: ''
            }
        }
    },
    created () {
        this.$nextTick(() => this.getData())
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/emp/authRoleInit', {}
            , function (data) {
                This.roles = data.data.roles
                This.emps = data.data.emps
                This.auth = data.data.auth
                This.getChecked()
            }, function (err) {
                console.log(err)
            })
        },
        changeIndex (index, id) {
            this.rolesIndex = index
            let This = this
            this.$ajax('/back/emp/getRoleAuthsDetails', {
                id: id
            }, function (data) {
                This.emps = data.data.emps
                This.auth = data.data.auth
                This.getChecked()
            }, function (err) {
                console.log(err)
            })
        },
        getChecked () {
            let obj = this.auth
            this.authList = []
            for (let i in obj) {
                for (let j = 0, len = obj[i].length; j < len; j++) {
                    if (obj[i][j].flag) {
                        this.authList.push(obj[i][j].priPindex)
                    }
                };
            }
        },
        saveAuth () {
            let This = this
            this.$ajax('/back/emp/modifyRoleAuths', {
                role: {
                    id: this.roles[this.rolesIndex].id
                },
                priviliges: this.authList.join(',')
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
           this.alertParames.isShow = false
        },
        makesureAlert () {
            let This = this
            if (this.addData.r_name === '') {
                This.msg.content = '不能为空'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/emp/addRole', {
                role: {
                    r_name: this.addData.r_name,
                    description: this.addData.description
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.alertParames.isShow = false
                    This.getData()
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.role-permissions {
    display: flex;
    justify-content: space-around;
    & > div {
        padding: 10px;
    }
}
.title {
    border-bottom: 2px solid #289cff;
    span {
        display: inline-block;
        vertical-align: middle;
        padding: 0 20px;
        line-height: 30px;
        color: #666;
        font-size: 16px;
    }
    .btn {
        cursor: pointer;
        color: #4eb9f5;
    }
}
h4 {
    border-bottom: 1px solid #e4e4e4;
    line-height: 40px;
    padding: 0 10px;
    margin: 0;
}
.rolelist-detail {
    border: 1px solid #e4e4e4;
    li li {
        line-height: 40px;
        padding: 0 10px;
        cursor: pointer;
        color: #666;
    }
    .active, li li:hover {
        background-color: #95d4ff;
    }
}
.role-detail {
    li li {
        line-height: 30px;
        padding: 0 10px;
        color: #666;
        label {
            cursor: pointer;
        }
    }
}
.memberList table {
    min-width: 260px;
}
.auth-list, .emps-list {
    padding: 0 20px;
    width: 270px;
    height: 650px;
    overflow: hidden;
    overflow-y: scroll;
}
.auth-list input{
    vertical-align: middle;
}
.row {
    width: 400px;
    margin: 10px auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #666;
    p {
        text-align: right;
        width: 100px;
    }
    textarea {
        outline: none;
        padding: 10px;
    }
}
.btn {
    padding-top: 10px;
    text-align: center;
    span {
        display: inline-block;
        padding: 6px 20px;
        background-color: #289cff;
        height: 20px;
        border-radius: 4px;
        color: #fff;
        cursor: pointer;
        border: 0;
        &:hover {
            text-decoration: underline;
        }
    }
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



