<template>
<div class="user-info">
    <div class="row">
        <span class="title">姓名：</span>
        <span>{{ userData.userName }}</span>
    </div>
    <div class="row">
        <span class="title">身份证号码：</span>
        <span>{{ userData.idCode }}</span>
    </div>
    <div class="row">
        <span class="title">手机号码：</span>
        <span>{{ userData.mobile }}</span>
    </div>
    <div class="row">
        <span class="title">用户名：</span>
        <span>{{ userData.loginName }}</span>
    </div>
    <div class="row">
        <span class="title">银行卡号：</span>
        <span>{{ userData.cardNo }}</span>
    </div>
    <div class="row">
        <span class="title">推荐人：</span>
        <span>{{ userData.refferral }}</span>
    </div>
    <div class="row">
        <span class="title">最后登录时间：</span>
        <span>{{ userData.lastLoginDate }}</span>
    </div>
    <div class="row">
        <span class="title">注册时间：</span>
        <span>{{ userData.registerDate }}</span>
    </div>
    <div class="row">
        <span class="ready">{{ userData.idCode ? '已开户' : '未开户' }}</span>
    </div>
</div>
</template>

<script>
export default {
    name: 'user-info',
    data () {
        return {
            userData: {}
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/user/searchUserDetail', {
                'id': this.$route.query.id
            }, function (data) {
                This.userData = data.data.userDetail
            }, function (err) {
                console.log(err)
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.user-info {
    padding: 20px;
    overflow: hidden;
    zoom: 1;
}
.row {
    float: left;
    width: 48%;
    line-height: 40px;
    color: #333;
}
.row .title {
    color: #999;
}
.ready {
    color: #ff6f0a;
}
</style>



