<template>
<div class="plat-child">
    <h3>平台不同子账户转账</h3>
    <div class="padding-top">
        来源账户：<selection :index-default="accountTypeIndex" :arr="accountTypeList" @selected="accountTypeFn"></selection>
    </div>
    <div class="padding-top">
        目标账户：<selection :index-default="targetIndex" :arr="accountTypeList" @selected="targetFn"></selection>
    </div>
    <p>
        <label>转入金额：</label>
        <input v-model="transAmount" type="text" placeholder=""/>
        元
    </p>
    <p>
        <a @click="sendData" class="btn submit">提交申请</a>
    </p>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'plat-child',
    data () {
        return {
            accountTypeList: [
                {
                    lable: '自有子账户',
                    val: '1'
                },
                {
                    lable: '手续费子账户',
                    val: '3'
                },
                {
                    lable: '体验金子账户',
                    val: '4'
                },
                {
                    lable: '抵用金子账户',
                    val: '5'
                },
                {
                    lable: '加息金子账户',
                    val: '6'
                },
                {
                    lable: '保证金子账户',
                    val: '7'
                }
            ],
            accountTypeIndex: 0,
            targetIndex: 0,
            transAmount: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        sendData () {
            let This = this
            if (!this.transAmount) {
                This.msg.content = '请输入转入金额'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/platform/accountConverse', {
                'source_account': this.accountTypeList[this.accountTypeIndex].val,
                'dest_account': this.accountTypeList[this.targetIndex].val,
                'amount': this.transAmount
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.transAmount = ''
                }
            }, function (err) {
                console.log(err)
            })
        },
        accountTypeFn (index) {
            this.accountTypeIndex = index
        },
        targetFn (index) {
            this.targetIndex = index
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        selection,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    width: 100px;
    height: 36px;
    line-height: 36px;
    display: block;
    margin-left: 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
    user-select: none;
}
.submit {
    margin-left: 100px;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.padding-top {
    padding-top: 10px;
}
</style>



