<template>
<div class="project-detail">
    <p @click="goback" class="goback">返回</p>
    <p class="head">
        <span>{{ project.title }}</span>
    </p>
    <div>
        <table>
            <tbody>
                <tr>
                    <th>标的id：</th>
                    <td>{{ project.id }}</td>
                    <th>产品类型：</th>
                    <td>{{ project.productName }}</td>
                    <th>标的总金额（元）：</th>
                    <td>{{ project.amount }}</td>
                </tr>
                <tr>
                    <th>借款人ID：</th>
                    <td>{{ project.guaranteeId }}</td>
                    <th>关联企业：</th>
                    <td>{{ project.guaranteeRealm }}</td>
                    <th>借款期限（月）：</th>
                    <td>{{ project.months }}</td>
                </tr>
                <tr>
                    <th>年利率：</th>
                    <td>{{ project.rate }}</td>
                    <th>还款方式：</th>
                    <td>{{ project.method |  method }}</td>
                    <th>投资人群：</th>
                    <td>{{ project.groupId ? '新用户' : '全部用户' }}</td>
                </tr>
                <tr>
                    <th>起投金额（元）：</th>
                    <td>{{ project.minAmount }}</td>
                    <th>最大投资金额（元）：</th>
                    <td>{{ project.maxAmount }}</td>
                    <th>投资增量（元）：</th>
                    <td>{{ project.stepAmount }}</td>
                </tr>
                <tr>
                    <th>担保费率：</th>
                    <td>{{ project.loanGuaranteeFee }}</td>
                    <th>服务费率：</th>
                    <td>{{ project.loanServiceFee }}</td>
                    <th>风险管理费率：</th>
                    <td>{{ project.loanRiskFee }}</td>
                </tr>
                <tr>
                    <th>借款管理费率：</th>
                    <td>{{ project.loanManageFee }}</td>
                    <th>还款（借款）利息管理费率：</th>
                    <td>{{ project.loanInterestFee }}</td>
                    <th>回款（投资）利息管理费率：</th>
                    <td>{{ project.investInterestFee }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <p class="bid-detail-p">公司简介：<span>{{ project.firmInfo }}</span></p>
    <p class="bid-detail-p">经营范围：<span>{{ project.operationRange }}</span></p>
    <p class="bid-detail-p">资金用途：<span>{{ project.description }}</span></p>
    <p class="bid-detail-p">还款来源：<span>{{ project.repaySource }}</span></p>
    <p class="bid-detail-p">风险措施：<span>{{ project.riskInfo }}</span></p>
    <p>
        <!-- <img src="../../img/errorBg.png" alt="图片" />
        <img src="../../img/errorBg.png" alt="图片" />
        <img src="../../img/errorBg.png" alt="图片" />
        <img src="../../img/iconBg.gif" alt="图片" /> -->
    </p>
    <div class="search-bar">
        <ul class="nav">
            <li class="active">已发布标的</li>
        </ul>
        <selection :index-default="statusIndex" :arr="projectStatus" @selected="statusFn" keystr="name"></selection>
        <div>标的总额：<span>{{ totalAmount }}</span> 元</div>
    </div>
    <table>
        <thead>
            <tr>
                <th>标的名称</th>
                <th>唯一号</th>
                <th>标的金额</th>
                <th>已投资金额</th>
                <th>标的期限</th>
                <th>还款方式</th>
                <th>状态</th>
                <th>开标时间</th>
                <th>结算时间</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in loans">
                <td>{{ item.title }}</td>
                <td>{{ item.serial }}</td>
                <td>{{ item.amount }}</td>
                <td>{{ item.bidAmount }}</td>
                <td>{{ item.months }}</td>
                <td>{{ item.method | method }}</td>
                <td>{{ item.status | status }}</td>
                <td><span v-if="item.timeOpen">{{ item.timeOpen | date }}</span></td>
                <td><span v-if="item.timeSettled">{{ item.timeSettled | date }}</span></td>
            </tr>
            <tr v-if="!loans.length">
                <td colspan="9">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../submodel/selection.vue'
import alert from '../submodel/alert.vue'
export default {
    name: 'project-detail',
    props: {
        dataObj: {
            type: Object,
            default: function () {
                return {}
            }
        }
    },
    data () {
        return {
            projectStatus: [
                {
                    name: '全部',
                    val: 'ALL'
                },
                {
                    name: '初始',
                    val: 'INITIATED'
                },
                {
                    name: '未安排',
                    val: 'UNPLANED'
                },
                {
                    name: '已安排',
                    val: 'SCHEDULED'
                },
                {
                    name: '开放投标',
                    val: 'OPENED'
                },
                {
                    name: '流标',
                    val: 'FAILED'
                },
                {
                    name: '已满标',
                    val: 'FINISHED'
                },
                {
                    name: '已取消',
                    val: 'CANCELED'
                },
                {
                    name: '已结算',
                    val: 'SETTLED'
                },
                {
                    name: '已还清',
                    val: 'CLEARED'
                },
                {
                    name: '逾期',
                    val: 'OVERDUE'
                },
                {
                    name: '违约',
                    val: 'BREACH'
                },
                {
                    name: '已存档',
                    val: 'ARCHIVED'
                }
            ],
            statusIndex: 0,
            project: {},
            loans: [],
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            totalAmount: 0
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    components: {
        selection,
        alert
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/project/searchProjectByParams', {
                project: {
                    id: this.dataObj.id
                },
                status: this.projectStatus[this.statusIndex].val
            }, function (res) {
                if (res.data.message.code === 1000) {
                    This.project = res.data.project
                    This.loans = res.data.loans
                    This.totalAmount = res.data.totalAmount
                } else {
                    This.msg.content = res.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        statusFn (index) {
            this.statusIndex = index
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        },
        goback () {
            this.$emit('backFn')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.search-bar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    line-height: 40px;
    ul {
        margin-right: 30px;
        .active {
            padding: 0 30px;
            background-color: #75bcec;
            color: #fff;
        }
    }
}
.goback {
    display: inline-block;
    padding: 4px 30px;
    color: #5ea7ea;
    cursor: pointer;
    font-size: 16px;
}
</style>



