<template>
<div class="bdxx">
    <p class="head">
        标的ID：<input v-model="prodId" type="text" placeholder="必填" size="50"/>&nbsp;&nbsp;
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>平台编号</th>
                    <th>标的ID</th>
                    <th>标的名称</th>
                    <th>标的账号</th>
                    <th>标的状态</th>
                    <th>标的总额</th>
                    <th>已投</th>
                    <th>未投</th>
                    <th>年利率</th>
                    <th>是否自动出账</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{{ obj.plat_no }}</td>
                    <td>{{ obj.prod_id }}</td>
                    <td>{{ obj.prod_name }}</td>
                    <td>{{ obj.prod_account }}</td>
                    <td>{{ obj.prod_state }}</td>
                    <td>{{ obj.total_limit }}</td>
                    <td>{{ obj.saled_limit }}</td>
                    <td>{{ obj.remain_limit }}</td>
                    <td>{{ obj.ist_year }}</td>
                    <td>{{ obj.chargeOff_auto }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'bdxx',
    data () {
        return {
            obj: {},
            prodId: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.prodId) {
                This.msg.content = '请输入标的ID'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchLoanInfo', {
                'prod_id': this.prodId
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.loanInfo || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



