import axios from 'axios'
import router from '../../router'

const state = {
	loginStatus: false,
	user: {
		name: ''
	}
}

const getters = {
	getLoginStatus: state => state.loginStatus,
	getUser: state => state.user.name
}

const actions = {
	getUserInfo ({ commit, state }) {
		axios.post('/Account_Manager_Back/back/emp/isLogin', {})
		.then((res) => {
			commit('updateUserInfo', res.data)
			if (res.data.message.code === 2002) {
				router.push({path: 'login'})
			}
		}, (err) => {
			console.log(err)
		})
	}
}

const mutations = {
	updateUserInfo (state, backInfo) {
		if (backInfo.message.code === 1000) {
			state.loginStatus = true
			state.user.name = backInfo.emp.empName
		} else {
			state.loginStatus = false
			state.user.name = ''
		}
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}



