<template>
<div class="three-payment">
    <div>
        <h4>
            <span>商户充值</span>
            <span class="mini">(单笔充值金额应大于1元小于100万元)</span>
        </h4>
        <p>
            <label for="merchantRecharge">充值金额</label>
            <input v-model="chargeAmount" id="merchantRecharge" type="text"/><span> 元</span>
            <a @click="chargeFn" class="btn">充值</a>
        </p>
    </div>
    <div class="contBox">
        <h4>
            <span>商户提现</span>
            <!-- <span class="mini">(无需审核，直接提现到默认卡)</span> -->
        </h4>
        <p>
            <label for="merchantWithdraw">提现金额</label>
            <input v-model="withdrawAmount" id="merchantWithdraw" type="text"/><span> 元</span>
            <a @click="withdrawFn" class="btn">提现</a>
        </p>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'three-payment',
    data () {
        return {
            chargeAmount: '',
            withdrawAmount: '',
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        chargeFn () {
            let This = this
            this.$ajax('/back/platform/platCharge', {
                amount: this.chargeAmount
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        withdrawFn () {
            let This = this
            this.$ajax('/back/platform/platWithdraw', {
                amount: this.withdrawAmount
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.contBox {
    border-top: 1px solid #ddd;
}
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
    margin: 0 6px;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



