<template>
<div class="ddzt">
    <p>
        明细订单号：<input v-model="parame.ordNo" type="text" placeholder="必填" size="30"/>&nbsp;
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>平台编号</th>
                    <th>订单号</th>
                    <th>订单状态</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{{ obj.plat_no }}</td>
                    <td>{{ obj.query_order_no }}</td>
                    <td>{{ obj.status }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'ddzt',
    data () {
        return {
            obj: {},
            parame: {
                ordNo: ''
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.parame.ordNo) {
                This.msg.content = '请输入明细订单号'
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchOrderStatus', {
                'query_order_no': this.parame.ordNo

            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.orderInfo || {}
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



