<template>
<div class="artical">
    <div class="head">
        <div class="aLink">
            <router-link :to="{path: '/new-artical'}">新建文章</router-link>
            <router-link :to="{path: '/column'}">编辑栏目</router-link>
        </div>
        <selection :index-default="columuIndex" keystr="name" :arr="columuList" @selected="selectionCallback"></selection>
        <div>
            <my-datepicker @changed="dateChange" time="start"></my-datepicker>
            <span @click="searchFn" class="btn">搜索</span>
            <span @click="positionFn" class="btn warning">确定位置</span>
        </div>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>序号</th>
                    <th>文章标题</th>
                    <th>类别</th>
                    <th>时间</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td>{{ (parame.now - 1) * 10 + index + 1 }}</td>
                    <td>{{ item.title }}</td>
                    <td>{{ item.columnName }}</td>
                    <td>{{ item.submitTime }}</td>
                    <td>
                        <router-link :to="{path: '/new-artical', query: {id: item.id}}">编辑</router-link>
                        <a @click="changePisition('up', item, index)" href='javascript:void(0);'>向上</a>
                        <a @click="changePisition('down', item, index)" href='javascript:void(0);'>向下</a>
                        <a @click="changePisition('top', item, index)" href='javascript:void(0);'>置顶</a>
                        <a @click="del(item, index)" href='javascript:void(0);'>删除</a>
                    </td>
                </tr>
                <tr v-if="!list.length">
                    <td colspan="5">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import selection from '../../submodel/selection.vue'
import pagination from '../../submodel/pagination.vue'
import myDatepicker from '../../submodel/my-datepicker.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'artical',
    data () {
        return {
            list: [],
            columuList: [],
            columuIndex: 0,
            parame: {
                searchInput: '',
                now: 1,
                all: 1
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    components: {
        selection,
        pagination,
        myDatepicker,
        alert
    },
    created () {
        this.$nextTick(() => {
            this.getColumn()
        })
    },
    methods: {
        getColumn () {
            let This = this
            this.$ajax('/back/cms/queryCmsColumnByParams', {}, function (data) {
                if (data.data.message.code === 1000) {
                    This.columuList = data.data.columnvos
                    This.getData()
                }
            }, function (err) {
                console.log(err)
            })
        },
        getData () {
            let This = this
            this.$ajax('/back/article/queryArticleByParams', {
                page: {
                    pageNo: this.parame.now,
                    pageSize: 20,
                    params: {
                        columnId: this.columuList[this.columuIndex].id,
                        title: this.parame.searchInput
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.pageArticleVo.results
                }
            }, function (err) {
                console.log(err)
            })
        },
        searchFn () {
            this.getData()
        },
        del (obj, index) {
            let This = this
            this.$ajax('/back/article/deleteArticleByParams', {
                article: {
                    id: obj.id
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.list.splice(index, 1)
                }
            }, function (err) {
                console.log(err)
            })
        },
        changePisition (str, obj, index) {
            let num = 0
            if (str === 'up' && index !== 0) {
                num = index - 1
            }
            if (str === 'down' && index <= (this.list.length - 1)) {
                num = index + 1
            }
            if (str === 'top') {
                num = 0
            }
            this.list.splice(index, 1)
            this.list.splice(num, 0, obj)
        },
        positionFn () {
            let This = this
            let arr = []
            this.list.forEach(function (item) {
                arr.push(item.id)
            })
            this.$ajax('/back/article/posDisplace', {
                str: arr
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        selectionCallback (index) {
            this.columuIndex = index
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getData()
        },
        dateChange (val, type) {
            this.parame.searchInput = val.time
        },
        closeMsg () {
            this.msg.isShow = false
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.head {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 50px;
}
.aLink a, .btn {
    display: inline-block;
    padding: 4px 8px;
    background-color: #38afff;
    color: #fff;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;
}
.aLink a:hover, .btn:hover {
    text-decoration: underline;
}
.warning {
    background-color: #ff5825;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
</style>



