<template>
<div class="pic">
	<div class="head">
		<div class="links">
			<a @click="tab(0)" :class="{active: index === 0}" href="javascript:">pc-banner</a>
			<a @click="tab(1)" :class="{active: index === 1}" href="javascript:">app-banner</a>
			<a @click="tab(2)" :class="{active: index === 2}" href="javascript:">广告</a>
			<a @click="tab(3)" :class="{active: index === 3}" href="javascript:">新闻图片</a>
		</div>
		<div>
			<div class="imgUpload">
                <input type="file" @change="onFileChange($event)" multiple ref='input'>
                <span class="upload-btn choose-img">选择图片</span>
                <span @click="uploadImage()" class="upload-btn">上传图片</span>
                <span @click="reView" class="btn save">预览</span>
            </div>
		</div>
	</div>
	<table>
		<thead>
			<tr>
				<th>图片</th>
				<th>标题</th>
				<th>链接</th>
				<th>时间</th>
				<th>操作</th>
			</tr>
		</thead>
		<tbody>
			<tr v-for="(item, index) in imgList.upload">
				<td><img :src="item" alt=""></td>
				<td></td>
				<td></td>
				<td></td>
				<td><a @click="delLocal(index)" href="javascript:">删除</a></td>
			</tr>
            <tr v-if="imgList.callback.length" v-for="(item, index) in imgList.callback">
                <td><img :src="item.pathAddress" alt=""></td>
				<td><input type="text" v-model="item.title"></td>
				<td><input type="text" v-model="item.jumpAddress"></td>
				<td><span v-if="item.time">{{ item.time | date }}</span></td>
				<td>
					<a @click="del(item.pathAddress, index)" href="javascript:">删除</a>
					<a @click="saveItem(item)" href="javascript:">保存修改</a>
					<a @click="changePisition('up', item, index)" href='javascript:void(0);'>向上</a>
                    <a @click="changePisition('down', item, index)" href='javascript:void(0);'>向下</a>
                    <a @click="changePisition('top', item, index)" href='javascript:void(0);'>置顶</a>
				</td>
            </tr>
		</tbody>
	</table>
	<div @click="navShow = false" v-if="navShow" class="nav">
		<div @click.stop="navFn('prev')" class="prev">&lt;</div>
		<div @click.stop="" class="nav-wrap">
			<img v-if="imgList.callback.length" :src="imgList.callback[navIndex].pathAddress">
		</div>
		<div @click.stop="navFn('next')" class="next">&gt;</div>
	</div>
	<alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import VueImageUpload from 'vue-core-image-upload'
import alert from '../../submodel/alert.vue'
export default {
	name: 'pic',
	data () {
		return {
			index: 0,
			navIndex: 0,
			navShow: false,
			imgList: {
                upload: [],
                callback: []
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
		}
	},
	components: {
		VueImageUpload,
		alert
	},
	created () {
		this.$nextTick(() => {
			this.getImg()
		})
	},
	methods: {
		getImg () {
			let This = this
			let type = ''
            if (this.index === 0) {
            	type = 'pc_banner'
            } else if (this.index === 1) {
            	type = 'app_banner'
            } else if (this.index === 2) {
            	type = 'advertisement'
            } else if (this.index === 3) {
            	type = 'news'
            }
			this.$ajax('/back/upload/searchPhotoManager', {
                type: type
            }, function (data) {
            	if (data.data.message.code === 1000) {
                    This.imgList.callback = data.data.photoManagers
                }
            }, function (err) {
                console.log(err)
            })
		},
		saveItem (obj) {
			let This = this
			this.$ajax('/back/upload/modifyPhoto', {
				photo: {
					id: obj.id,
	                title: obj.title,
	                jumpAddress: obj.jumpAddress
				}
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    data.data.path.forEach((item) => {
                        This.imgList.callback.push(item)
                    })
                }
            }, function (err) {
                console.log(err)
            })
		},
		onFileChange (e) {
            let files = e.target.files || e.dataTransfer.files
            if (!files.length) return
            this.createImage(files)
        },
        createImage (file) {
            if (typeof FileReader === 'undefined') {
                alert('您的浏览器不支持图片上传，请升级您的浏览器')
                return false
            }
            var This = this
            var leng = file.length
            for (var i = 0; i < leng; i++) {
                var reader = new FileReader()
                reader.readAsDataURL(file[i])
                reader.onload = function (e) {
                	This.imgList.upload.push(e.target.result)
                }
            }
        },
        uploadImage () {
            if (this.imgList.upload.length <= 0) {
                this.msg.content = '没有可上传的图片，如需上传图片，请先点击选择图片'
                this.msg.time = '1500'
                this.msg.isShow = true
                return
            }
            let This = this
            let type = ''
            if (this.index === 0) {
            	type = 'pc_banner'
            } else if (this.index === 1) {
            	type = 'app_banner'
            } else if (this.index === 2) {
            	type = 'advertisement'
            } else if (this.index === 3) {
            	type = 'news'
            }
            this.$ajax('/back/upload/uploadPhoto', {
                files: This.imgList.upload,
                type: type
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.imgList.upload = []
                    This.getImg()
                }
            }, function (err) {
                console.log(err)
            })
        },
        delLocal (index) {
        	this.imgList.upload.splice(index, 1)
        },
        del (url, index) {
        	let This = this
        	this.$ajax('/back/upload/deletePhotoManager', {
                url: url
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.imgList.callback.splice(index, 1)
                }
            }, function (err) {
                console.log(err)
            })
        },
        tab (num) {
        	this.index = num
        	this.imgList.upload = []
			this.imgList.callback = []
        	this.getImg()
        },
        changePisition (str, obj, index) {
        	let num = 0
            if (str === 'up' && index !== 0) {
                num = index - 1
            }
            if (str === 'down' && index <= (this.imgList.callback.length - 1)) {
                num = index + 1
            }
            if (str === 'top') {
                num = 0
            }
            this.imgList.callback.splice(index, 1)
            this.imgList.callback.splice(num, 0, obj)
        },
        reView () {
        	this.navShow = true
        	let This = this
        	let arr = []
        	let type = ''
            if (this.index === 0) {
            	type = 'pc_banner'
            } else if (this.index === 1) {
            	type = 'app_banner'
            } else if (this.index === 2) {
            	type = 'advertisement'
            } else if (this.index === 3) {
            	type = 'news'
            }
        	this.imgList.callback.forEach(function (item, index) {
        		arr.push({
        			pathAddress: item.pathAddress,
        			serialNumber: index
        		})
        	})
        	this.$ajax('/back/upload/modifyPhotoBySerial', {
                photoManagers: arr,
                type: type
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        navFn (str) {
        	if (str === 'prev') {
        		this.navIndex --
        	}
        	if (str === 'next') {
        		this.navIndex ++
        	}
        	if (this.navIndex < 0) {
        		this.navIndex = this.imgList.callback.length - 1
        	}
        	if (this.navIndex >= this.imgList.callback.length) {
        		this.navIndex = 0
        	}
        },
        closeMsg () {
            this.msg.isShow = false
        }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.head {
	display: flex;
	justify-content: space-around;
    align-items: center;
    padding: 6px 0;
    margin-bottom: 10px;
    line-height: 40px;
    border-bottom: 1px solid #38afff;
}
.links a {
	display: inline-block;
	padding: 0 30px;
	color: #333;
}
.links .active, .links a:hover {
	color: #eb6100;
}
.btn {
	display: inline-block;
	padding: 0 14px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    vertical-align: middle;
}
.save {
	margin-left: 10px;
	background-color: #ff5825;
}
.imgUpload {
    position: relative;
    input[type='file'] {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 9;
        width:56px;
        opacity: 0;
    }
}
.upload-btn {
    display: inline-block;
    padding: 6px 30px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
.nav {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.7);
	display: flex;
	justify-content: space-around;
    align-items: center;
    .nav-wrap {
    	max-width: 80%;
    }
    img {
    	display: block;
    }
    .prev, .next {
    	color: #fff;
    	font-size: 60px;
    	cursor: pointer;
    	user-select: none;
    }
}
input {
	height: 100%;
	width: 80%;
	padding: 0 10%;
}
input[type='text'] {
	border: 1px solid #ddd;
	height: 30px;
	line-height: 30px;
}
td {
	table-layout:fixed;
	max-width: 400px;
}
td img {
	height: 80px;
	display: block;
	margin: 0 auto;
}
</style>



