<template>
<div class="over-due">
    <v-search @changeVal="dateChange" @searchFn="searchBtn"></v-search>
    <table>
        <thead>
            <tr>
                <th>标的名称</th>
                <th>借款人</th>
                <th>期数</th>
                <th>待还金额</th>
                <th>标的账户余额</th>
                <th>到期日</th>
                <th>标的还款状态</th>
                <th>借款人是否还款</th>
                <th>操作</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ item.title }}</td>
                <td>{{ item.userName }}</td>
                <td>{{ item.currentPeriod }}</td>
                <td>{{ item.amountInterest + item.amountPrincipal }}</td>
                <td>{{ item.account_aviamount }}</td>
                <td>{{ item.dueDate }}</td>
                <td>{{ item.status | repayStatus }}</td>
                <td>{{ item.isRepay ? "已还" : "未还" }}</td>
                <td><a v-if="item.isRepay" @click="payFn(item)" href='javascript:void(0);'>标的还款</a></td>
            </tr>
            <tr v-if="!list.length && isFinished">
                <td colspan="9">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <repayment-popup @closePopup="isShow = false" :detail="payDate" :repay-type="repayment" :is-show="isShow"></repayment-popup>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import repaymentPopup from '../../submodel/repayment-popup.vue'
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'over-due',
    data () {
        return {
            list: [],
            parame: {
                start: '',
                end: '',
                now: 1,
                all: 1
            },
            repayment: 'pay',
            payDate: {},
            isShow: false,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            isFinished: false
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            this.$ajax('/back/loanRepayment/searchLoanRepaymentList', {
                page: {
                    'strStartTime': this.parame.start,
                    'strEndTime': this.parame.end,
                    'pageNo': this.parame.now,
                    'pageSize': 10,
                    params: {
                        'status': 'OVERDUE'
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results
                    This.parame.all = data.data.page.totalPage
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
                This.isFinished = true
            }, function (err) {
                console.log(err)
            })
        },
        dateChange (start, end) {
            this.parame.start = start
            this.parame.end = end
        },
        searchBtn () {
            this.getData()
        },
        pagingCallback (index) {
            this.parame.now = index
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
            this.closeFn()
        }
    },
    components: {
        'v-search': search,
        repaymentPopup,
        pagination,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



