<template>
<div class="bid-list">
    <p class="head">
        <a @click="changeStatus(0)" href="javascript:" :class="{active: statusIndex===0}">全部</a>
        <a @click="changeStatus(1)" href="javascript:" :class="{active: statusIndex===1}">进行中</a>
        <a @click="changeStatus(2)" href="javascript:" :class="{active: statusIndex===2}">满标</a>
        <a @click="changeStatus(3)" href="javascript:" :class="{active: statusIndex===3}">还款中</a>
        <a @click="changeStatus(4)" href="javascript:" :class="{active: statusIndex===4}">已还清</a>
    </p>
    <div class="search-bar">
        <span>产品类型</span>
        <selection :index-default="productIndex" keystr="name" :arr="productType" @selected="productFn"></selection>
        <span class="select-lable">担保公司</span>
        <selection :index-default="companyIndex" keystr="shortname" :arr="companyList" @selected="companyFn"></selection>
        <span class="select-lable">标的名称</span>
        <input class="searchBid" v-model="parame.title" type="text" placeholder="标的名称" size="20">
        <v-search class="inline" @searchFn="searchFn" @changeVal="searchValChange"></v-search>
        <span class='export-btn'>导出</span>
    </div>
    <div>
        <table>
            <thead>
                <tr>
                    <th>序号</th>
                    <th>标的名称</th>
                    <th>借款期限（月）</th>
                    <th>关联企业</th>
                    <th>标的金额（元）</th>
                    <th>平台标的账户余额（元）</th>
                    <th>状态</th>
                    <th>发布时间</th>
                    <th>结算时间</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in list">
                    <td>{{ (parame.now-1)*10+index+1 }}</a></td>
                    <td>{{ item.title }}</td>
                    <td>{{ item.months }}</td>
                    <td>{{ item.guaranteeRealm }}</td>
                    <td>{{ item.amount }}</td>
                    <td>{{ item.tenderAmount }}</td>
                    <td>{{ item.status | status }}</td>
                    <td>{{ item.timeOpen }}</td>
                    <td>{{ item.timeSettled }}</td>
                </tr>
                <tr v-if="!list.length">
                    <td colspan="9">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
    <p>总计标的金额： <strong>{{ totalAmount }}</strong> 元</p>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import selection from '../../submodel/selection.vue'
import alert from '../../submodel/alert.vue'
import pagination from '../../submodel/pagination.vue'
export default {
    name: 'bid-list',
    data () {
        return {
            list: [],
            totalAmount: 0,
            productType: [],
            productIndex: 0,
            companyList: [],
            companyIndex: 0,
            parame: {
                title: '',
                now: 1,
                all: 1,
                startTime: '',
                endTime: ''
            },
            statusIndex: 0,
            statusList: ['all', 'OPENED', 'FINISHED', 'SETTLED', 'CLEARED'],
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    created () {
        this.$nextTick(() => {
            this.getAllProducts()
            this.getCompany()
        })
    },
    methods: {
        getData () {
            let This = this
            This.msg.content = '查询中...'
            This.msg.isShow = true
            this.$ajax('/back/loan/loanList', {
                page: {
                    pageNo: this.parame.now,
                    pageSize: 10,
                    strStartTime: this.parame.startTime,
                    strEndTime: this.parame.endTime,
                    params: {
                        productId: this.productType[this.productIndex].id,
                        guaranteeId: this.companyList[this.companyIndex].userId,
                        title: this.parame.title,
                        status: this.statusList[this.statusIndex]
                    }
                }
            }, function (data) {
                This.msg.isShow = false
                if (data.data.message.code === 1000) {
                    This.totalAmount = data.data.totalAmount || 0
                    This.list = data.data.page.results || []
                    This.parame.all = data.data.page.totalPage
                } else {
                    setTimeout(() => {
                        This.msg.content = data.data.message.message
                        This.msg.time = '1500'
                        This.msg.isShow = true
                    }, 0)
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        getAllProducts () {
            let This = this
            this.$ajax('/back/product/queryAllProducts', {}, function (data) {
                This.productType = data.data.products
                This.productType.unshift({
                    description: '全部',
                    id: '',
                    name: '全部'
                })
            }, function (err) {
                console.log(err)
            })
        },
        getCompany () {
            let This = this
            this.$ajax('/back/cor/queryAllCorporationUserByCondition', {}, function (data) {
                This.companyList = data.data
                This.companyList.unshift({
                    description: '全部',
                    userId: '',
                    shortname: '全部'
                })
            }, function (err) {
                console.log(err)
            })
        },
        changeStatus (num) {
            this.statusIndex = num
            this.getData()
        },
        productFn (num) {
            this.productIndex = num
        },
        companyFn (num) {
            this.companyIndex = num
        },
        searchFn () {
            this.getData()
        },
        searchValChange (start, end) {
            this.parame.startTime = start
            this.parame.endTime = end
        },
        pagingCallback (num) {
            this.parame.now = num
            this.getData()
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        'v-search': search,
        selection,
        alert,
        pagination
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.head a {
    display: inline-block;
    padding: 0 10px; 
}
.head .active {
    color: red;
}
.select-lable {
    display: inline-block;
    margin: 0 6px;
    color: #333;
    font-size: 14px;
}
.searchBid {
    padding: 0 6px;
    line-height: 30px;
}
.search-btn, .export-btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.export-btn {
    background-color: #ff5825;
}
.inline {
    display: inline-block;
}
input {
    border: 1px solid #e4e4e4;
    padding: 6px 10px;
}
</style>



