                <p @click.stop="userSetView = !userSetView" class="welcome">欢迎，{{ username }}<i class="iconfont icon-moreunfold"></i></p>
                <div @click.stop="" v-show="userSetView" class="msg">
                    <p @click="loginOut"><i class="iconfont icon-delete"></i>退出登录</p>
                </div>
            </div>
        </div>
        <div class="content-wrap">
            <div class="tab-link">
                <div v-for="(item, index) in navList">
                    <template v-if="item.name.link !== ''">
                        <router-link @click.native="tap(index)" tag="h3" :to="{path: item.name.link}"><i class="iconfont" :class="item.name.icon"></i>{{item.name.describe}}</router-link>
                    </template>
                    <template v-else>
                        <h3 @click="tap(index)"><i class="iconfont" :class="item.name.icon"></i>{{item.name.describe}}</h3>
                    </template>
                    <transition name="fade">
                        <ul v-show="navShowIndex==index">
                            <router-link v-for="child in item.include" tag="li" :to="{path: child.link}">{{child.describe}}</router-link>
                        </ul>
                    </transition>
                </div>
            </div>
            <div class="content">
                <router-view></router-view>
            </div>
        </div>
        <alert @close="closeAlert" @cancel="cancelAlert" @makesure="makesureAlert" :parames="alertParames"></alert>
        <alert :parames="msg" @close="closeMsg"></alert>
    </div>
