                            link: '/modify-password'
                        }
                    ]
                }
            ]
export default {
    name: 'app',
    data () {
        return {
            userSetView: false,
            navShowIndex: 0,
            navList: navList,
            alertParames: {
                isShow: false,
                width: 400,
                showTitle: true,
                title: '你确定要退出吗？',
                content: '',
                cancel: '取消',
                makesure: '确定'
            },
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    computed: {
        username () {
            return this.$store.getters.getUser
        },
        loginStatus () {
            return this.$store.getters.loginStatus
        }
    },
    components: {
        alert
    },
    created () {
        this.$nextTick(() => {
            this.$store.dispatch('getUserInfo')
        })
    },
    mounted () {
        this.navShowIndex = sessionStorage.getItem('navShowIndex') || 0
    },
    methods: {
        tap (index) {
            this.navShowIndex = index
            sessionStorage.setItem('navShowIndex', index)
        },
        loginOut () {
            this.alertParames.isShow = true
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            this.alertParames.isShow = false
        },
        makesureAlert () {
            let This = this
            this.$ajax('/back/emp/emplogout', {}, function (data) {
                This.$store.commit('updateUserInfo', {
                    message: {
                        code: 1001,
                        message: '退出成功'
                    }
                })
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
                if (data.data.message.code === 1000) {
                    This.$router.push({path: 'login'})
                }
            }, function (err) {
                console.log(err)
            })
            this.alertParames.isShow = false
        },
        closeMsg () {
            this.msg.isShow = false
        }
    }
}
