<template>
<div class="datepicker">
    <datepicker :date="timeType" :option="option" :limit="limit"></datepicker>
</div>
</template>

<script>
import Datepicker from 'vue-datepicker/vue-datepicker-es6.vue'
export default {
    name: 'date',
    props: {
        time: {
            type: String,
            required: true
        },
        type: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            start: {
                time: ''
            },
            end: {
                time: ''
            },
            detail: {
                time: ''
            },
            option: {
                type: 'day',
                week: ['一', '二', '三', '四', '五', '六', '日'],
                month: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
                format: 'YYYY-MM-DD',
                placeholder: '点击选择时间',
                inputStyle: {
                    'display': 'inline-block',
                    'padding': '6px',
                    'line-height': '22px',
                    'font-size': '16px',
                    'border': '2px solid #fff',
                    'box-shadow': '0 1px 2px 2px rgba(0, 0, 0, 0.2)',
                    'border-radius': '2px',
                    'color': '#5F5F5F'
                },
                overlayOpacity: 0.5,
                dismissible: true
            },
            limit: []
        }
    },
    watch: {
        start: {
            handler: function (val, oldVal) {
                this.dateChange(val, 'start')
            },
            deep: true
        },
        end: {
            handler: function (val, oldVal) {
                this.dateChange(val, 'end')
            },
            deep: true
        },
        detail: {
            handler: function (val, oldVal) {
                this.dateChange(val, 'detail')
            },
            deep: true
        }
    },
    methods: {
        dateChange (val, type) {
            this.$emit('changed', val, type)
        }
    },
    computed: {
        timeType: function () {
            let str = ''
            if (this.time === 'start') {
                str = this.start
            } else if (this.time === 'end') {
                str = this.end
            } else if (this.time === 'detail') {
                str = this.detail
            }
            return str
        }
    },
    mounted () {
        if (this.type === '' || this.type === 'day') {
            this.option.type = 'day'
            this.option.format = 'YYYY-MM-DD'
        } else if (this.type === 'min') {
            this.option.type = 'min'
            this.option.format = 'YYYY-MM-DD HH:mm:ss'
        }
    },
    components: {
        Datepicker
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.datepicker {
    display: inline-block;
}
</style>



