<template>
<div class="user-lend">
    <table>
        <thead>
            <tr>
                <th>标题</th>
                <th>金额</th>
                <th>年利率</th>
                <th>期限(月)</th>
                <th>还款方式</th>
                <th>标的状态</th>
                <th>投资日期</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ item.title }}</td>
                <td>{{ item.amount }}</td>
                <td>{{ item.rate }} <span class="add-rate" v-if="item.addRate">+ {{ item.addRate }}</span></td>
                <td>{{ item.months }}</td>
                <td>{{ item.method | method }}</td>
                <td>{{ item.status | status }}</td>
                <td>{{ item.strSubmitTime }}</td>
            </tr>
            <tr v-if="!list.length">
                <td colspan="7">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <pagination v-if="all>1" class="paging" @pagingFn="pagingCallback" :now="now" :all="all"></pagination>
</div>
</template>

<script>
import pagination from '../../submodel/pagination.vue'
export default {
    name: 'user-lend',
    data () {
        return {
            now: 1,
            all: 1,
            list: []
        }
    },
    components: {
        pagination
    },
    created () {
        this.getList()
    },
    methods: {
        getList () {
            let This = this
            this.$ajax('/back/invest/searchInvestRecordByParams', {
                'page': {
                    pageNo: this.now,
                    pageSize: 10,
                    params: {
                        userId: this.$route.query.id
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results
                    This.all = data.data.page.totalPage
                }
            }, function (err) {
                console.log(err)
            })
        },
        pagingCallback (num) {
            this.now = num
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.user-lend {
    padding-top: 10px;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
.add-rate {
    color: #ff6f0a;
    font-size: 12px;
}
</style>



