<template>
<div class="pagination">
    <a v-show="pageNow > 3 && all > 5" @click="junpTo('start')" href="javascript:">&lt;</a>
    <a v-show="pageNow != 1" @click="junpTo('prev')" href="javascript:">上一页</a>
    <a @click="junpTo(item)" :class="{on:(item===pageNow)}" v-for="item in stepNum" href="javascript:">{{item}}</a>
    <a v-show="pageNow != all" @click="junpTo('next')" href="javascript:">下一页</a>
    <a v-show="pageNow < (all - 2) && all > 5" @click="junpTo('end')" href="javascript:">&gt;</a>
    <span>To</span>
    <input @change="gotoFn" v-model="textVal" type="text">
</div>
</template>

<script>
export default {
    name: 'pagination',
    props: {
        now: {
            type: Number,
            default: 2
        },
        all: {
            type: Number,
            default: 5
        },
        step: {
            type: Number,
            default: 5
        }
    },
    data () {
        return {
            pageNow: 1,
            textVal: ''
        }
    },
    watch: {
        pageNow: function (val, oldval) {
            this.$emit('pagingFn', val)
        }
    },
    mounted () {
        this.pageNow = this.now
    },
    computed: {
        stepNum: function () {
            let list = []
            let start = 1
            let allNum = this.all
            let junpNum = (allNum <= this.step) ? allNum : this.step
            if (this.pageNow < 3 || allNum <= this.step) {
                start = 1
            } else if (allNum - this.pageNow <= 2) {
                start = allNum - this.step + 1
            } else {
                start = this.pageNow - 2
            }
            for (let i = 0, len = junpNum; i < len; i++) {
                list.push(start + i)
            }
            return list
        }
    },
    methods: {
        junpTo (num) {
            if (num === 'start') {
                this.pageNow = 1
            }
            if (num === 'end') {
                this.pageNow = this.all
            }
            if (num === 'prev') {
                this.pageNow = (this.pageNow >= 2) ? this.pageNow - 1 : 1
            }
            if (num === 'next') {
                this.pageNow = (this.all - this.pageNow >= 1) ? this.pageNow + 1 : this.all
            }
            if (typeof num === 'number') {
                this.pageNow = num
            }
        },
        gotoFn () {
            let goNum = parseInt(this.textVal)
            if (goNum > 0 && goNum <= this.all) {
                this.pageNow = goNum
            }
            if (!goNum) {
                alert('请输入正确的数值')
            }
            if (goNum > this.all) {
                alert('你输入的值超过了最大值')
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.pagination {
    line-height: 36px;
    user-select: none;
}
.on {
    background: #289cff;
    color: #fff;
}
a, span, input {
    display: inline-block;
    vertical-align: middle;
    margin: 0 3px;
    font-size: 14px;
    color: #5a5a5a;
}
a {
    height: 22px;
    padding: 4px 12px;
    line-height: 22px;
    border: 1px solid #e4e4e4;
}
span {
    margin-left: 30px;
}
input {
    width: 40px;
    height: 20px;
    padding: 4px;
    border: 1px solid #e4e4e4;
    text-align: center;
    line-height: 20px;
}
</style>



