<template>
<div class="edite-user">
    <div class="edit">
        <i @click="closeFn">×</i>
        <h4>编辑用户</h4>
        <ul>
            <li>
                <label>手机号码：</label>
                <span>{{ parentData.mobile }}</span>
            </li>
            <li>
                <label>姓名：</label>
                <span>{{ parentData.userName }}</span>
            </li>
            <li>
                <label>身份证号：</label>
                <span>{{ parentData.idCode }}</span>
            </li>
            <li>
                <label>客户类型：</label>
                <selection :index-default="userTypeIndex" :arr="userType" @selected="userTypeFn"></selection>
            </li>
            <li>
                <label>启用/禁用：</label>
                <selection :index-default="disableIndex" :arr="disable" @selected="disableFn"></selection>
            </li>
            <li v-if="parentData.isLock">
                <label>解锁：</label>
                <input type="checkbox" value="false" v-model="isLock">
            </li>
        </ul>
        <input @click="saveFn" class="btn" type="button" value="确定"/>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
import selection from '../../submodel/selection.vue'
export default {
    name: 'edite-user',
    props: {
        parentData: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    data () {
        return {
            userType: [
                {
                    lable: '投资用户',
                    val: 1
                },
                {
                    lable: '借款用户',
                    val: 2
                }
            ],
            userTypeIndex: 0,
            disable: [
                {
                    lable: '禁用',
                    val: false
                },
                {
                    lable: '启用',
                    val: true
                }
            ],
            disableIndex: 0,
            isLock: [],
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    components: {
        alert,
        selection
    },
    created () {
        this.$nextTick(() => {
            this.userTypeIndex = this.getIndex(this.parentData.userType, this.userType, 'val')
            this.disableIndex = this.getIndex(this.parentData.enabled, this.disable, 'val')
        })
    },
    methods: {
        saveFn () {
            let fl = this.isLock.length === 0 ? true : false
            let This = this
            this.$ajax('/back/user/modifyUser', {
                user: {
                    'id': this.parentData.id,
                    'enabled': this.disable[this.disableIndex].val,
                    'isLock': fl,
                    'userType': this.userType[this.userTypeIndex].val
                }
            }, function (data) {
                This.msg.content = data.data.message.message
                This.msg.time = '1500'
                This.msg.isShow = true
            }, function (err) {
                console.log(err)
            })
        },
        userTypeFn (val) {
            this.userTypeIndex = val
        },
        disableFn (val) {
            this.disableIndex = val
        },
        closeAlert () {
            this.alertParames.isShow = false
        },
        cancelAlert () {
            console.log('cencel')
        },
        makesureAlert () {
            console.log('ok')
        },
        getIndex (val, arr, key) {
            let i = 0
            arr.forEach(function (item, index) {
                if (item[key] === val) {
                    i = index
                }
            })
            return i
        },
        closeFn () {
            this.$emit('closeAlert')
        },
        closeMsg () {
            this.msg.isShow = false
            this.closeFn()
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.edite-user {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 9;
    background-color: rgba(0,0,0,0.5);
}
.edit {
    position: relative;
    background-color: #fff;
    padding: 20px;
    width: 300px;
    margin: 0 auto;
}
.btn {
    padding: 6px 30px;
    text-align: center;
    color: #fff;
    border-radius: 4px;
    background-color: #5cb85c;
    cursor: pointer;
    margin: 10px 0 0 100px;
}
h4 {
    margin:0 0 14px;
    text-align: center;
}
i {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 20px;
    height: 20px;
    text-align: center;
    color: #333;
    cursor: pointer;
    border-radius: 50%;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
}
</style>



