<template>
<div class="bdtzmx">
    <p>
        标的ID：<input v-model="prodId" type="text" placeholder="必填" size="50"/>&nbsp;&nbsp;
        页码：<input v-model="now" type="text" placeholder="必填" size="2">&nbsp;&nbsp;
        <span @click="getData" class="btn">查询</span>
    </p>
    <div>
        <table>
            <thead>
                <tr>
                    <th>平台名称</th>
                    <th>产品名称</th>
                    <th>加息利率</th>
                    <th>客户编号</th>
                    <th>客户名称</th>
                    <th>交易份额</th>
                    <th>交易日期</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in obj">
                    <td>{{ item.plat_name }}</td>
                    <td>{{ item.prod_name }}</td>
                    <td>{{ item.in_interest }}</td>
                    <td>{{ item.platcust }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.vol }}</td>
                    <td v-show="item.trans_date">{{ item.trans_date + ' ' + item.trans_time }}</td>
                </tr>
            </tbody>
        </table>
    </div>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import alert from '../../submodel/alert.vue'
export default {
    name: 'bdtzmx',
    data () {
        return {
            obj: [],
            prodId: '',
            now: 1,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            }
        }
    },
    methods: {
        getData () {
            let This = this
            if (!this.prodId || !this.now) {
                if (!this.prodId) {
                    This.msg.content = '请输入标的ID'
                } else if (!this.now) {
                    This.msg.content = '请输入页码'
                }
                This.msg.time = '1500'
                This.msg.isShow = true
                return
            }
            this.$ajax('/back/search/searchLoanInvest', {
                'prod_id': this.prodId
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.obj = data.data.loanInvestDetails || []
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
            }, function (err) {
                console.log(err)
            })
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.btn {
    display: inline-block;
    padding: 6px 20px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
input {
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    border: 1px solid #e4e4e4;
}
</style>



