<template>
<div class="not-due">
    <v-search @changeVal="dateChange" ref="projectSearch">
        <template slot="other">
            <span class="select-lable">借款人</span>
            <input v-model="parame.realName" class="searchBid" type="text" placeholder="借款人或手机号">
        </template>
        <template slot="btn">
            <span @click="searchFn" class='search-btn'>查询</span>
            <span class='export-btn'>导出</span>
        </template>
    </v-search>
    <table>
        <thead>
            <tr>
                <th>到期日</th>
                <th>标的名称</th>
                <th>借款人</th>
                <th>期数</th>
                <th>待还金额</th>
                <th>标的账户余额</th>
                <th>标的还款状态</th>
                <th>借款人是否还款</th>
                <th>操作</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list">
                <td>{{ item.dueDate }}</td>
                <td>{{ item.title }}</td>
                <td>{{ item.userName }}</td>
                <td>{{ item.currentPeriod }}</td>
                <td>{{ item.amountInterest + item.amountPrincipal }}</td>
                <td>{{ item.account_aviamount }}</td>
                <td>{{ item.status | repayStatus }}</td>
                <td>{{ item.isRepay ? "已还" : "未还" }}</td>
                <td>
                    <a v-if="item.isRepay" @click="payFn(item)" href='javascript:void(0);'>提前还款</a>
                </td>
            </tr>
            <tr v-if="!list.length && isFinished">
                <td colspan="9">暂无数据</td>
            </tr>
        </tbody>
    </table>
    <pagination v-if="parame.all>1" class="paging" @pagingFn="pagingCallback" :now="parame.now" :all="parame.all"></pagination>
    <repayment-popup @closePopup="isShow = false" :detail="payDate" :repay-type="repayment" :is-show="isShow"></repayment-popup>
    <alert :parames="msg" @close="closeMsg"></alert>
</div>
</template>

<script>
import search from '../../submodel/search'
import repaymentPopup from '../../submodel/repayment-popup.vue'
import pagination from '../../submodel/pagination.vue'
import alert from '../../submodel/alert.vue'
export default {
    name: 'not-due',
    data () {
        return {
            list: [],
            parame: {
                start: '',
                end: '',
                now: 1,
                all: 1,
                realName: ''
            },
            repayment: 'pay',
            payDate: {},
            isShow: false,
            msg: {
                isShow: false,
                content: '',
                time: '',
                contentStyle: 'text-align: center;'
            },
            isFinished: false
        }
    },
    created () {
        this.$nextTick(() => {
            this.getData()
        })
    },
    methods: {
        getData () {
            let This = this
            if (this.parame.start !== '') {
                let d = new Date()
                let t =  d.getFullYear() + '-' + (d.getMonth()+1) + '-' + (d.getDate() + 1)
                if (new Date(this.parame.start).getTime() < new Date(t).getTime()) {
                    This.msg.content = '开始时间不能小于明天'
                    This.msg.time = '1500'
                    This.msg.isShow = true
                    return
                }
            }
            this.$ajax('/back/loanRepayment/searchLoanRepaymentList', {
                page: {
                    'strStartTime': this.parame.start,
                    'strEndTime': this.parame.end,
                    'pageNo': this.parame.now,
                    'pageSize': 10,
                    params: {
                        'status': 'UNDUE',
                        'loanUser': this.parame.realName,
                        'unrepay': 'yes'
                    }
                }
            }, function (data) {
                if (data.data.message.code === 1000) {
                    This.list = data.data.page.results
                    This.parame.all = data.data.page.totalPage
                } else {
                    This.msg.content = data.data.message.message
                    This.msg.time = '1500'
                    This.msg.isShow = true
                }
                This.isFinished = true
            }, function (err) {
                This.isFinished = true
                console.log(err)
            })
        },
        payFn (obj) {
            let This = this
            this.msg.content = '还款中...'
            this.msg.isShow = true
            this.$ajax('/back/loan/repayLoan', {
                loanRepayment: {
                    'id': obj.id
                }
            }, function (data) {
                This.msg.isShow = false
                setTimeout(() => {
                    This.msg.content = data.data.message.message
                    This.msg.time = '2000'
                    This.msg.isShow = true
                }, 0)
                if (data.data.message.code === 1000) {
                    This.getData()
                }
            }, function (err) {
                This.msg.isShow = false
                console.log(err)
            })
        },
        pagingCallback (index) {
            this.parame.now = index
            this.getData()
        },
        searchFn () {
            this.getData()
        },
        dateChange (start, end) {
            this.parame.start = start
            this.parame.end = end
        },
        closeMsg () {
            this.msg.isShow = false
        }
    },
    components: {
        'v-search': search,
        repaymentPopup,
        pagination,
        alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.select-lable {
    display: inline-block;
    margin: 0 6px;
    color: #333;
    font-size: 14px;
}
.searchBid {
    padding: 0 6px;
    line-height: 30px;
}
.search-btn, .export-btn {
    display: inline-block;
    padding: 6px 20px;
    margin-left: 16px;
    background-color: #38afff;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}
.export-btn {
    background-color: #ff5825;
}
.paging {
    padding: 10px 20px;
    text-align: right;
}
</style>



